import EventAggregator from "ezyvuecore/core/events/eventAggregator";
import { CommandManager } from "../commandManager";
import SelectObjectCommand from "../commands/selectObjectCommand";
import { SceneModifiedEvent } from "../editorEvents";
import { Editor } from "../editor";

export class SceneGraphViewModel {
    constructor() {
        this.updateViewState = this.updateViewState.bind(this);
        this.onObjectClicked = this.onObjectClicked.bind(this);
        this.onNodeToggle = this.onNodeToggle.bind(this);
        this.onNodeSelected = this.onNodeSelected.bind(this);

        this.expandedNodeIds = [];
        this.selectedNodeIds = [];

        EventAggregator.instance.subscribe(SceneModifiedEvent, () => {
            this.createTree();
        });

        this.createTree();

    }

    createTree() {
        this.tree = this.buildTree(Editor.world.sceneRoot);

        this.updateViewState();
    }

    buildTree(object) {
        const node = {
            name: object.name,
            id: object.uuid,
            children: [],
            object: object
        };

        this.expandedNodeIds.push(node.id);

        object.children.forEach(child => {
            node.children.push(this.buildTree(child));
        });

        return node;
    }

    updateViewState(view = null) {
        if (view)
            this.view = view;

        if (this.view) {
            this.view.setState({
                tree: this.tree,
                expandedNodeIds: this.expandedNodeIds,
                selectedNodeIds: this.selectedNodeIds
            });
        }
    }

    onNodeToggle(e, ids) {
        this.expandedNodeIds = [...ids];

        this.updateViewState();
    }

    onNodeSelected(e, id) {
        this.selectedNodeIds = [id];

        this.updateViewState();
    }

    onObjectClicked(object) {
        CommandManager.execute(new SelectObjectCommand(object));
    }
}