import BoundingGeometry from "ezyvuecore/engine/three/boundingGeometry";
import { Mesh, Raycaster, Vector2, Vector3 } from "ezyvuecore/three/build/three";

const vec2 = new Vector2();

export function getCameraFloorIntersection(camera, scene) {
    const rayCaster = new Raycaster(camera.position, new Vector3(0, -1, 0), 0, 1000.0);

    const intersects = rayCaster.intersectObjects(scene, true);
    if (intersects.length)
        return intersects[0].point;

    return null;
}

export function renderToDataUrl(world) {
    const width = 1920;
    const height = 1080;

    world.renderer.renderer.getSize(vec2);

    if (world.camera) {
        world.camera.aspect = width / height;
        world.camera.updateProjectionMatrix();
    }

    world.renderer.renderer.setSize(width, height);
    world.composer.setSize(width, height);

    world.renderScene();

    if (world.camera) {
        world.camera.aspect = vec2.x / vec2.y;
        world.camera.updateProjectionMatrix();
    }

    const dataUrl = world.renderer.canvas.toDataURL('image/jpeg', 0.5);

    world.composer.setSize(vec2.x, vec2.y);
    world.renderer.renderer.setSize(vec2.x, vec2.y);

    world.renderScene();

    return dataUrl;
}

export function snapModelToGround(scene) {
    let boundingGeometry = calculateBoundingGeometry(scene);

    scene.position.y = scene.position.y - boundingGeometry.boundingBox.min.y;
}

export function calculateBoundingGeometry(model) {
    const boundingGeometry = new BoundingGeometry(model);

    return boundingGeometry;
}

export function getSceneMaterials(scene) {
    const materials = [];

    scene.traverse(object => {
        if (object instanceof Mesh) {
            if (!materials.find(m => m === object.material)) {
                materials.push(object.material);
            }
        }
    });

    return materials;
}

export function updateSceneMaterials(scene) {
    scene.traverse(object => {
        if (object instanceof Mesh) {
            object.material.needsUpdate = true;
        }
    });
}

export function getObjectType( object ) {
    if ( object.isScene ) return 'Scene';
    if ( object.isCamera ) return 'Camera';
    if ( object.isLight ) return 'Light';
    if ( object.isMesh ) return 'Mesh';
    if ( object.isLine ) return 'Line';
    if ( object.isPoints ) return 'Points';

    return 'Node';
}