import EventAggregator from "ezyvuecore/core/events/eventAggregator";
import { CommandManager } from "../commandManager";
import AddAnnotationCommand from "../commands/addAnnotationCommand";
import { Editor } from "../editor";
import { ProjectSettingsChangedEvent, RayObjectIntersectionEvent } from "../editorEvents";
import SetAnnotationsPropertyCommand from "../commands/setAnnotationsPropertyCommand";
import EditorSettings from "../editorSettings";
import { cloneDeep } from "lodash";

export default class AnnotationsViewModel {
    constructor() {
        this.viewState = {
            editMode: 'select',
            addButtonDisabled: false,
            labelText: 'Enter description',
            buttonText: '1'
        };

        this.updateViewState = this.updateViewState.bind(this);
        this.onEditModeOk = this.onEditModeOk.bind(this);
        this.onPropertyChanged = this.onPropertyChanged.bind(this);
        this.onPropertyBlur = this.onPropertyBlur.bind(this);
        this.onAddAnnotation = this.onAddAnnotation.bind(this);

        EventAggregator.instance.subscribe(ProjectSettingsChangedEvent, () => {
            this.updateViewState();
        });

        EventAggregator.instance.subscribe(RayObjectIntersectionEvent, event => {
            if (this.viewState.editMode === 'add') {
                const cmd = new AddAnnotationCommand(Editor.project.settings.state.annotations, event.intersect.point,
                    this.viewState.buttonText, this.viewState.labelText, event.intersect.object);

                CommandManager.execute(cmd);

                this.viewState.editMode = 'select';

                this.updateViewState();
            }
        });
    }

    onCommitProperty(name, value) {
        const newState = cloneDeep(Editor.project.settings.state.annotations);

        newState[name] = value;

        const cmd = new SetAnnotationsPropertyCommand(newState, Editor.project.settings.state.annotations);
        CommandManager.execute(cmd);

        this.updateViewState();
    }

    onPropertyChanged(e) {
        Editor.project.settings.state = {
            annotations: {
                [e.target.name]: e.target.value
            }
        }

        this.updateViewState();
    }

    onPropertyBlur(e) {
        if (!e.key || e.key === 'Enter')
            this.onCommitProperty(e.target.name, e.target.value);
    }

    onViewStatePropertyChanged(name, value) {
        this.viewState[name] = value;

        this.updateViewState();
    }

    onEditModeOk() {
        this.viewState.editMode = 'select';

        this.updateViewState();
    }

    onAddAnnotation() {
        this.viewState.editMode = 'add';

        this.updateViewState();
    }

    updateViewState(view = null) {
        if (view)
            this.view = view;

        if (this.view) {
            this.view.setState({
                viewState: this.viewState,
                modelState: Editor.project.settings.state.annotations
            });
        }
    }
}