import React from 'react';
import Tooltip from '@material-ui/core/Tooltip';
import Slider from '@material-ui/core/Slider';
import Checkbox from '@material-ui/core/Checkbox';

import PropTypes from 'prop-types'
import TextureSelector from '../textureSelector';

export default class MaterialView extends React.Component {
    constructor() {
        super();

        this.state = {};
    }

    render() {
        if(!this.props.viewModel || Object.keys(this.state).length === 0)
            return;
 
        return (
            <div className='material-pane'>
                <div className='flexrow'>
                    <div className='flexcol'>
                        Color
                    </div>
                    <div className='flexcol'>
                        <Tooltip title="Select Color">
                            <input type='color' value={this.state.color} onChange={this.props.viewModel.onColorChanged} />
                        </Tooltip>
                    </div>
                </div>
                <div className='flexrow'>
                    <div className='flexcol'>
                        Emissive Color
                    </div>
                    <div className='flexcol'>
                        <Tooltip title="Select Emissive Color">
                            <input type='color' value={this.state.emissive} onChange={this.props.viewModel.onEmissiveChanged} />
                        </Tooltip>
                    </div>
                </div>
                <div className='flexrow'>
                    <div className='flexcol'>
                        Roughness
                    </div>
                    <div className='flexcol'>
                        <Slider value={this.state.roughness} onChange={(e, value) => this.props.viewModel.onRoughnessChanged(value)} />
                    </div>
                </div>
                <div className='flexrow'>
                    <div className='flexcol'>
                        Metalness
                    </div>
                    <div className='flexcol'>
                        <Slider value={this.state.metalness} onChange={(e, value) => this.props.viewModel.onMetalnessChanged(value)} />
                    </div>
                </div>
                <div style={{ display: 'flex', justifyContent: 'left', alignItems: 'center' }}>
                    <div className='flexcol'>
                        Transparent
                    </div>
                    <div className='flexcol'>
                        <Checkbox checked={this.state.transparent} onChange={(e, value) => this.props.viewModel.onTransparentChanged(value)} />
                    </div>
                </div>
                <div className='flexrow'>
                    <div className='flexcol'>
                        Opacity
                    </div>
                    <div className='flexcol'>
                        <Slider value={this.state.opacity} onChange={(e, value) => this.props.viewModel.onOpacityChanged(value)} />
                    </div>
                </div>
                <div style={{ display: 'flex', justifyContent: 'left', alignItems: 'center' }}>
                    <div className='flexcol'>
                        Depth Test
                    </div>
                    <div className='flexcol'>
                        <Checkbox checked={this.state.depthTest} onChange={(e, value) => this.props.viewModel.onDepthTestChanged(value)} />
                    </div>
                </div>
                <div style={{ display: 'flex', justifyContent: 'left', alignItems: 'center' }}>
                    <div className='flexcol'>
                        Depth Write
                    </div>
                    <div className='flexcol'>
                        <Checkbox checked={this.state.depthWrite} onChange={(e, value) => this.props.viewModel.onDepthWriteChanged(value)} />
                    </div>
                </div>
                <div className='flexrow'>
                    <div className='flexcol'>
                        Map
                    </div>
                    <div className='flexcol'>
                        <TextureSelector texture={this.state.map} 
                            onChange={texture => this.props.viewModel.onMapChanged(texture, 'map')}
                            onDelete={() => this.props.viewModel.onMapDelete('map')} />
                    </div>
                </div>
                <div className='flexrow'>
                    <div className='flexcol'>
                        Bump Map
                    </div>
                    <div className='flexcol'>
                        <TextureSelector texture={this.state.bumpMap} 
                            onChange={texture => this.props.viewModel.onMapChanged(texture, 'bumpMap')}
                            onDelete={() => this.props.viewModel.onMapDelete('bumpMap')} />
                    </div>
                </div>
                <div className='flexrow'>
                    <div className='flexcol'>
                        Displacement Map
                    </div>
                    <div className='flexcol'>
                        <TextureSelector texture={this.state.displacementMap} 
                            onChange={texture => this.props.viewModel.onMapChanged(texture, 'displacementMap')}
                            onDelete={() => this.props.viewModel.onMapDelete('displacementMap')} />
                    </div>
                </div>
                <div className='flexrow'>
                    <div className='flexcol'>
                        AO Map
                    </div>
                    <div className='flexcol'>
                        <TextureSelector texture={this.state.aoMap} 
                            onChange={texture => this.props.viewModel.onMapChanged(texture, 'aoMap')}
                            onDelete={() => this.props.viewModel.onMapDelete('aoMap')} />
                    </div>
                </div>                    
                <div className='flexrow'>
                    <div className='flexcol'>
                        Emissive Map
                    </div>
                    <div className='flexcol'>
                        <TextureSelector texture={this.state.emissiveMap} 
                            onChange={texture => this.props.viewModel.onMapChanged(texture, 'emissiveMap')}
                            onDelete={() => this.props.viewModel.onMapDelete('emissiveMap')} />
                    </div>
                </div> 
                <div className='flexrow'>
                    <div className='flexcol'>
                        Environment Map
                    </div>
                    <div className='flexcol'>
                        <TextureSelector texture={this.state.envMap} 
                            onChange={texture => this.props.viewModel.onMapChanged(texture, 'envMap')}
                            onDelete={() => this.props.viewModel.onMapDelete('envMap')} />
                    </div>
                </div>
                <div className='flexrow'>
                    <div className='flexcol'>
                        Metalness Map
                    </div>
                    <div className='flexcol'>
                        <TextureSelector texture={this.state.metalnessMap} 
                            onChange={texture => this.props.viewModel.onMapChanged(texture, 'metalnessMap')}
                            onDelete={() => this.props.viewModel.onMapDelete('metalnessMap')} />
                    </div>
                </div> <div className='flexrow'>
                    <div className='flexcol'>
                        Normal Map
                    </div>
                    <div className='flexcol'>
                        <TextureSelector texture={this.state.normalMap}
                            onChange={texture => this.props.viewModel.onMapChanged(texture, 'normalMap')}
                            onDelete={() => this.props.viewModel.onMapDelete('normalMap')} />
                    </div>
                </div> <div className='flexrow'>
                    <div className='flexcol'>
                        Roughness Map
                    </div>
                    <div className='flexcol'>
                        <TextureSelector texture={this.state.roughnessMap} 
                            onChange={texture => this.props.viewModel.onMapChanged(texture, 'roughnessMap')}
                            onDelete={() => this.props.viewModel.onMapDelete('roughnessMap')} />
                    </div>
                </div>
                <div className='flexrow'>
                    <div className='flexcol'>
                        Light Map
                    </div>
                    <div className='flexcol'>
                        <TextureSelector texture={this.state.lightMap} 
                            onChange={texture => this.props.viewModel.onMapChanged(texture, 'lightMap')}
                            onDelete={() => this.props.viewModel.onMapDelete('lightMap')} />
                    </div>
                </div> 
                

                {/*   <div className='flexrow'>
                    <div className='flexcol'>Roughness</div>
                    <div className='flexcol'>
                        <Slider
                            value={this.state.modelState.roughness}
                            onChange={(e, value) => this.props.viewModel.onChange({ roughness: value })} />
                    </div>
                </div>
                <div className='flexrow'>
                    <div className='flexcol'>Metalness</div>
                    <div className='flexcol'>
                        <Slider
                            value={this.state.modelState.metalness}
                            onChange={(e, value) => this.props.viewModel.onChange({ metalness: value })} />
                    </div>
                </div>
                <div className='flexrow'>
                    <div className='flexcol'>
                        Emissive Color
                    </div>
                    <div className='flexcol'>
                        <Tooltip title="Select Color">
                            <input
                                type='color'
                                value={this.state.modelState.emissive}
                                onChange={(e) => this.props.viewModel.onChange({ emissive: e.target.value })} />
                        </Tooltip>
                    </div>
                </div>
                <div style={{ display: 'flex', justifyContent: 'left', alignItems: 'center' }}>
                    <div className='flexcol'>
                        Transparent
                    </div>
                    <div className='flexcol'>
                        <Checkbox
                            checked={this.state.modelState.transparent}
                            onChange={(e, value) => this.props.viewModel.onChange({ transparent: value })} />
                    </div>
                </div>
                <div className='flexrow'>
                    <div className='flexcol'>
                        Opacity
                    </div>
                    <div className='flexcol'>
                        <Slider
                            value={this.state.modelState.opacity}
                            onChange={(e, value) => this.props.viewModel.onChange({ opacity: value })} />
                    </div>
                </div>
                   {                               
                        this.state.modelState.refractionEnabled && 
                        <Fragment>
                            <div className='flexrow'>
                                <div className='flexcol'>
                                    Refractive
                                </div>
                                <div className='flexcol'>
                                    <Checkbox
                                        checked={this.state.modelState.refractive}
                                        onChange={(e, value) => this.props.viewModel.onChange({refractive: value})} />
                                </div>
                            </div>
                            <div className='flexrow'>
                                <div className='flexcol'>
                                    Refraction
                                </div>
                                <div className='flexcol'>
                                    <Slider
                                        value={this.state.modelState.refractionRatio} 
                                        onChange={(e, value) => this.props.viewModel.onChange({refractionRatio: value})} />
                                </div>                                    
                            </div>
                        </Fragment>
                    } 
                <div className='flexrow'>
                    <div className='flexcol'>
                        Side
                    </div>
                    <div className='flexcol'>
                        <Select
                            value={this.state.modelState.side}
                            onChange={e => this.props.viewModel.onChange({ side: e.target.value })}
                            style={{ minWidth: '100px', marginBottom: '5px' }} >
                            <option value={0}>Front</option>
                            <option value={1}>Back</option>
                            <option value={2}>Both</option>
                        </Select>
                    </div>
                </div>
                <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                    {
                        this.state.modelState.showResetButton &&
                        <div>
                            <Button
                                color='primary'
                                size='small'
                                variant='outlined'
                                onClick={this.props.viewModel.onResetClick}>
                                Reset
                            </Button>
                        </div>
                    }
                    {
                        this.state.modelState.showDeleteButton &&
                        <div>
                            <Button
                                color='primary'
                                size='small'
                                variant='outlined'
                                onClick={() => { this.setState({ deleteDialogOpen: true }); }}
                                Delete>DELETE
                            </Button>
                        </div>
                    }
                </div> */}
                <br />
            </div>
        )
    }
}

MaterialView.propTypes = {
    viewModel: PropTypes.object
}