import Command from "../command";
import { Editor } from "../editor";

export default class AddObjectCommand extends Command {
    constructor(object) {
        super();

        this.object = object;
    }

    get name() {
        return 'Add Object'
    }

    execute() {
        this.sphere = Editor.addObject(this.object);
    }

    undo() {
        Editor.removeObject(this.object);
    }

    redo() {
        this.execute();
    }
}