import React, { Fragment } from "react"
import TextField from '@material-ui/core/TextField';
import { PropTypes } from 'prop-types';

export default class CameraSettingsView extends React.Component {
    constructor() {
        super();

        this.state = {};
    }

    render() {
        if(!this.props.viewModel || Object.keys(this.state).length === 0)
        return;

        return (
            <Fragment>
                <div className='editor-pane'>
                    <TextField
                        label='Field of View'
                        value={this.state.fov}
                        name='fov'
                        style={{ maxWidth: '200px' }}
                        inputProps={{
                            min: 20,
                            max: 90
                        }}
                        onKeyUp={this.props.viewModel.onPropertyBlur}
                        onBlur={this.props.viewModel.onPropertyBlur}
                        onChange={this.props.viewModel.onPropertyChanged} />
                    <TextField
                        label='Near Plane'
                        value={this.state.near}
                        name='near'
                        style={{ maxWidth: '200px' }}
                        onKeyUp={this.props.viewModel.onPropertyBlur}
                        onBlur={this.props.viewModel.onPropertyBlur}
                        onChange={this.props.viewModel.onPropertyChanged} />
                    <TextField
                        label='Far Plane'
                        value={this.state.far}
                        name='far'
                        style={{ maxWidth: '200px' }}
                        onKeyUp={this.props.viewModel.onPropertyBlur}
                        onBlur={this.props.viewModel.onPropertyBlur}
                        onChange={this.props.viewModel.onPropertyChanged} />
                </div>
            </Fragment>
        );
    }
}

CameraSettingsView.propTypes = {
    viewModel: PropTypes.object
}