import React, { Fragment } from 'react'
import PropTypes from 'prop-types';
import PlayCircleFilledIcon from '@material-ui/icons/PlayCircleFilled';
import StopIcon from '@material-ui/icons/Stop';
import LoopIcon from '@material-ui/icons/Loop';

export default class AnimationsView extends React.Component {
    constructor() {
        super();

        this.state = {};
    }

    render() {
        if (!this.state?.clipsState?.clips?.length || !this.props.viewModel)
            return null;

        return (
            <Fragment>
                <div className='editor-sidebar-heading'>
                    Animations
                </div>
                <table className='animations-table'>
                    {
                        this.state.clipsState.clips.map((clip, i) => {
                            return (
                                <tr key={i}>
                                    <td>{clip.name}</td>
                                    <td>{clip.duration.toFixed(2)}s</td>
                                    <td>
                                        {
                                            !clip.playing ?
                                                <PlayCircleFilledIcon
                                                    onClick={() => this.props.viewModel.playClip(i)}
                                                    size='small' />
                                                :
                                                <StopIcon
                                                    onClick={() => this.props.viewModel.stopClip(i)}
                                                    size='small' />
                                        }
                                    </td>
                                    <td>
                                        <LoopIcon
                                            style={{ opacity: clip.loop ? '1.0' : '0.5' }}
                                            onClick={() => this.props.viewModel.toggleLoop(i)} />
                                    </td>
                                </tr>
                            );
                        })
                    }
                </table>
            </Fragment>
        );
    }
}

AnimationsView.propTypes = {
    viewModel: PropTypes.object
};