
import { Editor } from "../editor";
import SetObjectPropertyCommand from "./setObjectPropertyCommand";

export default class SetLightPropertyCommand extends SetObjectPropertyCommand {
    constructor(object, propName, value) {
        super(object, propName, value);
    }

    execute() {
        super.execute();

        if(this.object.helper)
            this.object.helper.update();
    }

    undo() {
        super.undo();

        Editor.updateLightHelper(this.object.constructor.name);
    }
}