import { cloneDeep } from "lodash";
import Command from "../command";
import { Editor } from "../editor";

export default class SetEnvironmentCommand extends Command {
    constructor(settings, prevSettings) {
        super();

        this.settings = cloneDeep(settings);
        this.prevSettings = cloneDeep(prevSettings);
    }

    get name() {
        return 'Set Environment Property';
    }

    execute() {
        Editor.project.settings.state = {environment: this.settings};

        Editor.setEnvironment();
    }

    undo() {
        Editor.project.settings.state = {environment: this.prevSettings};
        
        Editor.setEnvironment();
    }

    redo() {
        this.execute();   
    }
}