import React from "react";
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import PropTypes from 'prop-types';

export default class ViewMenu extends React.PureComponent {
    constructor(props) {
        super(props);

        this.executeCommand = this.executeCommand.bind(this);

        this.state = {
        };
    }

    executeCommand(name) {
        this.props.viewCommands && this.props.viewCommands.execute(name);
        this.props.onClose();
    }

    render() {
        return (
            <Menu
                anchorEl={this.props.anchorElem}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'right',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'center',
                }}
                keepMounted
                open={Boolean(this.props.anchorElem)}
                onClose={this.props.onClose} >
                <MenuItem onClick={() => this.executeCommand('frame scene')}>
                    Frame Scene
                </MenuItem>
                <MenuItem onClick={() => this.executeCommand('frame selection')}>
                    Frame Selection
                </MenuItem>
            </Menu>
        );
    }
}

ViewMenu.propTypes = {
    anchorElem: PropTypes.object,
    onClose: PropTypes.func,
    viewCommands: PropTypes.object,
}