import { BoxGeometry, CapsuleGeometry, CircleGeometry, CylinderGeometry, DodecahedronGeometry, IcosahedronGeometry, LatheBufferGeometry, LatheGeometry, Mesh, MeshStandardMaterial, PlaneGeometry, RingGeometry, SphereGeometry, TetrahedronGeometry, TorusGeometry, TubeGeometry } from "ezyvuecore/three/build/three";

export function createSphere() {
    const geometry = new SphereGeometry();
    const material = new MeshStandardMaterial();

    return new Mesh(geometry, material);
}

export function createBox() {
    const geometry = new BoxGeometry(); 
    const material = new MeshStandardMaterial(); 
    
    return new Mesh( geometry, material ); 
}

