import { cloneDeep } from "lodash";
import Command from "../command";
import { Editor } from "../editor";

export default class SetCameraControllerCommand extends Command {
    constructor(settings, prevSettings) {
        super();

        this.settings = cloneDeep(settings);
        this.prevSettings = cloneDeep(prevSettings);
    }

    get name() {
        return 'Set Controller Property';
    }

    execute() {
        Editor.project.settings.state = {cameraController: this.settings};

        Editor.setCameraController();
    }

    undo() {
        Editor.project.settings.state = {cameraController: this.prevSettings};

        Editor.setCameraController();
    }

    redo() {
        this.execute();
    }
}