import { Color, EquirectangularReflectionMapping } from "ezyvuecore/three/build/three";

export default class MaterialViewModel {
    constructor() {
        this.updateViewState = this.updateViewState.bind(this);
        this.onColorChanged = this.onColorChanged.bind(this);
        this.onEmissiveChanged = this.onEmissiveChanged.bind(this);
        this.onRoughnessChanged = this.onRoughnessChanged.bind(this);
        this.onMetalnessChanged = this.onMetalnessChanged.bind(this);
        this.onOpacityChanged = this.onOpacityChanged.bind(this);
        this.onTransparentChanged = this.onTransparentChanged.bind(this);
        this.onDepthTestChanged = this.onDepthTestChanged.bind(this);
        this.onDepthWriteChanged = this.onDepthWriteChanged.bind(this);
        this.onMapChanged = this.onMapChanged.bind(this);
        this.onMapDelete = this.onMapDelete.bind(this);
    }

    setMaterial(material) {
        this.material = material;

        this.view && this.setStateFromMaterial(); 
    }

    setStateFromMaterial() {
        this.view.setState({
            color: '#' + this.material.color.getHexString(),
            emissive: '#' + this.material.emissive.getHexString(),
            roughness: this.material.roughness * 100.0,
            metalness: this.material.metalness * 100.0,
            transparent: this.material.transparent,
            opacity: this.material.opacity * 100.0,
            depthTest: this.material.depthTest,
            depthWrite: this.material.depthWrite,
            map: this.material.map,
            aoMap: this.material.aoMap,
            bumpMap: this.material.bumpMap,
            displacementMap: this.material.displacementMap,
            emissiveMap: this.material.emissiveMap,
            envMap: this.material.envMap,
            lightMap: this.material.lightMap,
            metalnessMap: this.material.metalnessMap,
            normalMap: this.material.normalMap,
            roughnessMap: this.material.roughnessMap
        });
    }

    onColorChanged(e) {
        this.material.color = new Color(e.target.value);

        this.updateViewState();
    }

    onEmissiveChanged(e) {
        this.material.emissive = new Color(e.target.value);

        this.updateViewState();
    }

    onRoughnessChanged(value) {
        this.material.roughness = value / 100.0;

        this.updateViewState();
    }

    onMetalnessChanged(value) {
        this.material.metalness = value / 100.0;

        this.updateViewState();
    }

    onTransparentChanged(value) {
        this.material.transparent = value;

        this.updateViewState();

        this.material.needsUpdate = true;
    }

    onOpacityChanged(value) {
        this.material.opacity = value / 100.0;

        this.material.needsUpdate = true;

        this.updateViewState();
    }
    
    onDepthWriteChanged(value) {
        this.material.depthWrite = value;

        this.material.needsUpdate = true;

        this.updateViewState();
    }

    onDepthTestChanged(value) {
        this.material.depthTest = value;

        this.material.needsUpdate = true;

        this.updateViewState();
    }

    onMapChanged(texture, type) {
        if(type === 'envMap')
            texture.mapping = EquirectangularReflectionMapping;

        this.material[type] = texture;

        this.material.needsUpdate = true;

        this.updateViewState();
    }

    onMapDelete(type) {
        this.material[type] = null;

        this.material.needsUpdate = true;

        this.updateViewState();
    }

    updateViewState(view = null) {
        if (view)
            this.view = view;

        if (this.view && this.material)
            this.setStateFromMaterial();
    }
}