import { Color } from "ezyvuecore/three/build/three";

export default class Command {
    get name() { return ''; }

    get undoable() { return true; }

    setObjectValue(object, name, value) {
        if (object[name] instanceof Color)
            object[name].setStyle(value);
        else {
            if(typeof object[name] === 'number' && typeof value !== 'number')
                value = Number(value);

            object[name] = value;
        }
    }
}