import { set, get } from "lodash";
import Command from "../command";
import { Editor } from "../editor";

export default class SetEffectsCommand extends Command {
    constructor(name, value) {
        super();

        this.prevValue = get(Editor.project.settings.state.effects, name);
        this.propName = name;
        this.value = value;
    }

    get name() {
        return 'Set Effects Property';
    }

    setValue(value) {
        const state = {};

        set(state, this.propName, value);

        Editor.project.settings.state = {effects: state};

        Editor.setEffects();
    }

    execute() {
        this.setValue(this.value);
    }

    undo() {
        this.setValue(this.prevValue);
    }

    redo() {
        this.execute();   
    }
}