import React, { Fragment } from "react";
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import PropTypes from 'prop-types';
import Divider from '@material-ui/core/Divider';

export default class FileMenu extends React.Component {
    constructor() {
        super();

        this.fileInputRef = React.createRef();
        this.projectInputRef = React.createRef();
        this.dirInputRef = React.createRef();
    }

    render() {
        return (
            <Fragment>
                <Menu
                    anchorEl={this.props.anchorElem}
                    anchorOrigin={{
                        vertical: 'bottom',
                        horizontal: 'right',
                    }}
                    transformOrigin={{
                        vertical: 'top',
                        horizontal: 'center',
                    }}
                    keepMounted
                    open={Boolean(this.props.anchorElem && !this.props.disabled)}
                    onClose={this.props.onClose} >
                    <MenuItem onClick={
                        () => {
                            this.props.onClose();
                            this.props.editorViewModel.getUserResponse('Discard current project and lose changes?', () => {
                                this.props.fileCommands.execute('new project');
                            });
                        }}>
                        New Project
                    </MenuItem>
                    <MenuItem onClick={
                        () => {
                            this.props.onClose();
                            this.props.editorViewModel.getUserResponse('Discard current project and lose changes?', () => {
                                this.projectInputRef.current.click();
                            });
                        }}>
                        Load Project
                    </MenuItem>
                    <MenuItem onClick={
                        async () => {
                            this.props.onClose();
                            await this.props.fileCommands.execute('export project');
                        }}>
                        Save Project
                    </MenuItem>
                    <Divider />
                    <MenuItem onClick={
                        () => {
                            this.props.onClose();
                            this.fileInputRef.current.click();
                        }}>
                        Import 3D File
                    </MenuItem>
                    <MenuItem onClick={
                        () => {
                            this.props.onClose();
                            this.dirInputRef.current.click();
                        }}>
                        Import Directory
                    </MenuItem>
                    <MenuItem onClick={
                        () => {
                            this.props.onClose();
                            this.props.fileCommands.execute('export glb');
                        }}>
                        Export Glb File
                    </MenuItem>
                    <MenuItem onClick={
                        () => {
                            this.props.onClose();
                            this.props.fileCommands.execute('publish');
                        }}>
                        Publish
                    </MenuItem>     
                    <MenuItem onClick={() => { window.location = '/' }}>
                        Exit
                    </MenuItem>
                </Menu>
                <input
                    ref={this.fileInputRef} type="file" style={{ display: 'none' }}
                    onChange={
                        e => {
                            if (this.props.fileCommands) {
                                this.props.fileCommands.execute('import file', e.target.files);
                            }
                        }
                    } />
                <input ref={this.projectInputRef} type="file" style={{ display: 'none' }} accept='.zip'
                    onChange={
                        e => {
                            if (this.props.fileCommands) {
                                this.props.fileCommands.execute('import project', e.target.files[0]);
                            }
                            this.projectInputRef.current.value = '';
                        }
                    } />
                <input ref={this.dirInputRef} type="file" webkitdirectory='' style={{ display: 'none' }}
                    onChange={
                        e => {
                            if (this.props.fileCommands) {
                                this.props.fileCommands.execute('import dir', e.target.files);
                            }
                        }
                    } />
            </Fragment>
        );
    }
}

FileMenu.propTypes = {
    anchorElem: PropTypes.object,
    disabled: PropTypes.bool,
    onClose: PropTypes.func,
    fileCommands: PropTypes.object,
    editorViewModel: PropTypes.object
}