import React from "react";
import TextureCanvas from "./textureCanvas";
import PropTypes from "prop-types";
import CloseIcon from '@material-ui/icons/Close';
import { loadTexture } from "./textureFile";

export default class TextureSelector extends React.Component {
    constructor() {
        super();

        this.inputRef = React.createRef();
        this.onfileSelected = this.onfileSelected.bind(this);
    }

    async onfileSelected(e) {
        const tex = await loadTexture(e.target.files[0]);
        
        this.props.onChange(tex);
    }
    
    render() {
        return (
            <div onClick={() => this.inputRef.current.click()} style={{display: 'flex'}}>
                <TextureCanvas texture={this.props.texture} />
                <CloseIcon style={{fontSize: '14px'}} onClick={ e => {
                    this.props.onDelete();
                    e.stopPropagation();
                }} />
                <input ref={this.inputRef} type='file' style={{display: 'none'}} onChange={this.onfileSelected} />
            </div>
        );
    }
}

TextureSelector.propTypes = {
    texture: PropTypes.object,
    onChange: PropTypes.func,
    onDelete: PropTypes.func,
}

