import EditorTab from "members/features/editor/editorTab";
import Tooltip from '@material-ui/core/Tooltip';
import React from "react";
import { Fragment } from "react";
import EffectsViewModel from "../viewModels/effectsViewModel";
import EffectsView from "../views/effectsView";
import Camera from '@material-ui/icons/Camera';

export default class EffectsTab extends EditorTab {
    constructor() {
        super();

        this.viewModel = new EffectsViewModel();
    }

    renderButtonIcon() {
        return (
            <Camera />
        )
    }

    renderContent() {
        return (
            <Fragment>
                <div className='editor-sidebar-heading' style={{ fontSize: 'large' }}>
                    Effects Properties
                </div>
                <EffectsView viewModel={this.viewModel} ref={this.viewModel.updateViewState} />
            </Fragment>
        );
    }
}