import { Color, Vector3 } from "ezyvuecore/three/build/three";
import { Editor } from "../editor";
import Cookie from "ezyvuecore/core/storage/cookie";

export default class EditorSettingsViewModel {
    constructor() {
        this.onPropertyChanged = this.onPropertyChanged.bind(this);
        this.updateViewState = this.updateViewState.bind(this);

        this.viewState = { 
            gridOn: true,
            gridSize: 100,
            gridDivisions: 40,
            gridCenterColor: '#402020',
            gridColor: '#202020'
        };

        const vs = localStorage.getItem('editor-prefs');
        if(vs)
            this.viewState = JSON.parse(vs);

        this.setGrid();
    }

    onPropertyChanged(name, value) {
        this.viewState[name] = value;

        this.updateViewState();
        
        this.setGrid();

       localStorage.setItem('editor-prefs', JSON.stringify(this.viewState));
    }

    setGrid() {
        Editor.setGrid(this.viewState.gridSize, this.viewState.gridDivisions,  new Color(this.viewState.gridColor), 
            new Color(this.viewState.gridCenterColor), this.viewState.gridOn);
    }

    updateViewState(view = null) {
        if(view)
            this.view = view;

        if(this.view) {            
            this.view.setState({
                viewState: {...this.viewState}
            });
        }
    }
}