import { CommandManager } from "../commandManager";
import FrameAllCommand from "../commands/frameAllCommand";
import FrameSelectionCommand from "../commands/frameSelectionCommand";

export default class ViewCommands {
    execute(name) {
        let cmd;

        switch (name) {
            case 'frame selection':
                cmd = new FrameSelectionCommand();
                break;
            case 'frame scene':
                cmd = new FrameAllCommand();
                break;
        }

        if (cmd)
            CommandManager.execute(cmd);
    }
}