import BoundingGeometry from "ezyvuecore/engine/three/boundingGeometry";
import EffectManager from "ezyvuecore/engine/three/effectManager";
import { FirstPersonControls } from "ezyvuecore/engine/three/firstPersonControls";
import { OrbitControls } from "ezyvuecore/engine/three/orbitControls";
import Renderer from "ezyvuecore/engine/three/renderer";
import { createGradient, frameMesh } from "ezyvuecore/engine/three/util";
import World from "ezyvuecore/engine/three/world";
import { AmbientLight, BoxHelper, CameraHelper, Color, DirectionalLight, Fog, FogExp2, GridHelper, HemisphereLight, Mesh, Object3D, PCFSoftShadowMap, PerspectiveCamera, PointLight, Points, PointsMaterial, Quaternion, SpotLight, TextureLoader, Vector2, Vector3 } from "ezyvuecore/three/build/three";
import EventAggregator from "ezyvuecore/core/events/eventAggregator";
import { ViewportClickEvent, SceneModifiedEvent, ObjectDeseletedEvent, ObjectSelectedEvent, ObjectPositionChangedEvent, ObjectRotationChangedEvent, ObjectScaleChangedEvent, RayObjectIntersectionEvent, ProjectSettingsChangedEvent, SceneChangedEvent } from "./editorEvents";
import { ObjectSelector } from "./tools/objectSelector";
import ObjectTransformControls from "./tools/objectTransformControls";
import SelectObjectCommand from "./commands/selectObjectCommand";
import { CommandManager } from "./commandManager";
import DeselectObjectCommand from "./commands/deselectObjectCommand";
import { createBakedShadowPlane, createDepthFadeReflector, createShadowLight, createShadowPlane, updateAnnotation } from "./sceneElements";
import AnnotationLabels from "./annotationLabels";
import CameraAnimation from "ezyvuecore/engine/three/cameraAnimation";
import AnimationPlayer from "ezyvuecore/engine/three/animationPlayer";
import { DirectionalLightHelper, PointLightHelper, SpotLightHelper } from "./helpers/lightHelpers";
import { lightHelperImage } from "./helpers/lightHelperTexture";
import ResourceTracker from "ezyvuecore/engine/three/resourceTracker";
import { createBox } from "./primitives";
import Project from "./project";
import EditorSettings from "./editorSettings";

const col1 = new Color();
const col2 = new Color();
const v1 = new Vector3();
const v2 = new Vector3();
const v3 = new Vector3();

class _Editor {
    get orbitCameraController() { return this._orbitCameraController; }
    get firstPersonCameraController() { return this._firstPersonCameraController; }
    get cameraController() { return this._cameraController; }
    get world() { return this._world; }
    set cameraController(controller) { this._cameraController = controller; }
    get containerElem() { return this._containerElem; }
    get canvasElem() { return this._canvasElem; }
    get selectedObject() { return this._selectedObject; }
    get renderPass() { return this._renderPass; }
    get viewport() {
        return {
            width: this.containerElem.clientWidth,
            height: this.containerElem.clientHeight,
            aspect: this.containerElem.clientWidth / this.containerElem.clientHeight
        }
    }
    get effectsPreviewMode() { return this._effectsPreviewMode; }
    set effectsPreviewMode(value) { this._effectsPreviewMode = value; this.setEffects(); }
    get envPreviewMode() { return this._envPreviewMode; }
    set envPreviewMode(value) { this._envPreviewMode = value; this.setEnvironment(); }
    get transformControls() { return this._transformControls }
    get project() { return this._project; }
    set project(project) {
        this._project = project;

        this.removeLightHelpers();

        this.world.clearSceneRoot();

        this.world.sceneRoot.add(project.scene);

        project.scene.traverse(o => {
            if (o.isMesh) {
                o.receiveShadow = true;
                o.castShadow = true;
            }
            else if (o.isPoints) {
                // correct material settings
                o.material = new PointsMaterial({
                    color: 0xffffffff,
                    size: o.userData.size,
                    sizeAttenuation: false,
                    depthTest: false,
                    transparent: true,
                    map: o.material.map,
                });
            }
            else if (o.isLight)
                this.updateLight(o);

            if (o?.animations?.length) {
                o.animationPlayer = new AnimationPlayer(o, o.animations);
            }
        });

        this.bindCamerasToControllers(project.scene, 1.0);

        this.renderPass.camera = this.orbitCameraController.object;

        this.transformControls.camera = this.orbitCameraController.object;

        this.setLighting();
        this.setEnvironment();
        this.setCameraController();

        this.addLightHelpers();

        this.deselectAll();

        this.orbitCameraController.object.position.set(20, 20, 20);
        this.orbitCameraController.object.lookAt(0, 0, 0);

        this.orbitCameraController.update();

        this.updateAnnotations();

        this.createAllBakedShadows();

        this.sceneBBoxHelper && this.sceneBBoxHelper.setFromObject(project.scene);

        this.recalculateSceneBounds();

        EventAggregator.instance.notifyAll(new ProjectSettingsChangedEvent());
        EventAggregator.instance.notifyAll(new SceneChangedEvent());
        EventAggregator.instance.notifyAll(new SceneModifiedEvent());
    }

    constructor() {
        this._effectsPreviewMode = false;
        this._envPreviewMode = false;
    }

    initialize(canvasElem, containerElem) {
        this._canvasElem = canvasElem;
        this._containerElem = containerElem;

        this._project = new Project(new Object3D(), new EditorSettings());

        this.mouseDownPos = new Vector2();
        this.mouseUpPos = new Vector2();

        const orbitCam = this.createOrbitCamera();
        this.project.scene.add(orbitCam);

        const fpCam = this.createFirstPersonCamera();
        this.project.scene.add(fpCam);

        this._orbitCameraController = new OrbitControls(orbitCam, containerElem);
        this.orbitCameraController.enablePan = true;
        this.orbitCameraController.autoRotate = false;
        //        this.orbitCameraController.enableDamping = true;
        //        this.orbitCameraController.dampingFactor = 0.02;
        this.orbitCameraController.autoRotateSpeed = 0;
        this.orbitCameraController.saveState();

        orbitCam.position.set(20, 20, 20);
        orbitCam.lookAt(0, 0, 0);

        this.orbitCameraController.update();

        this._firstPersonCameraController = new FirstPersonControls(fpCam, containerElem);
        this.firstPersonCameraController.lookSpeed = 0.1;

        this.firstPersonCameraController.object.position.set(-10, 10, -10)

        this.cameraController = this.orbitCameraController;

        const renderer = new Renderer();
        renderer.init(canvasElem);

        const composer = new EffectManager(renderer.renderer);

        this._world = new World(renderer, composer, this.orbitCameraController);

        this._renderPass = composer.createRenderPass(this.world.scene, orbitCam, true);
        composer.addPass(this.renderPass);

        this.world.sceneRoot.add(this.project.scene);

        this.ambientLight = new AmbientLight(new Color(this.project.settings.state.lighting.ambientLightColor),
            this.project.settings.state.lighting.ambientLightIntensity);

        this.world.scene.add(this.ambientLight);

        this.world.renderer.renderer.shadowMap.enabled = this.project.settings.state.lighting.shadowsEnabled;
        this.world.renderer.renderer.shadowMap.type = PCFSoftShadowMap;

        const resizeObserver = new ResizeObserver(entries => {
            if (entries.length > 0) {
                const width = entries[0].target.clientWidth;
                const height = entries[0].target.clientHeight;
                const aspect = width / height;

                this.orbitCameraController.object.aspect = aspect;
                this.orbitCameraController.object.zoom = 1.;
                this.orbitCameraController.object.updateProjectionMatrix();

                this.firstPersonCameraController.object.aspect = aspect;
                this.firstPersonCameraController.object.zoom = 1.;
                this.firstPersonCameraController.object.updateProjectionMatrix();

                if (this.world.composer)
                    this.world.composer.setSize(width - 1, height);

                if (this.world.renderer)
                    this.world.renderer.setSize(width, height);
            }

        });

        resizeObserver.observe(containerElem);

        this.objectSelector = new ObjectSelector();

        this._transformControls = new ObjectTransformControls(this.world.camera, this.canvasElem);
        this.transformControls.onDragChanged = event => {
            this.orbitCameraController.enabled = !event.value;

            if (this.transformControls.object.isLight)
                this.updateLight(this.transformControls.object);

            this.recalculateSceneBounds();
        };

        this.world.scene.add(this.transformControls.controls);

        this.getMousePos = (elem, x, y) => {
            const rect = elem.getBoundingClientRect();

            return [(x - rect.left) / rect.width, (y - rect.top) / rect.height];
        }

        this.canvasElem.addEventListener('pointerdown', event => {
            this.mouseDownPos.fromArray(this.getMousePos(this.canvasElem, event.clientX, event.clientY));
        });

        this.canvasElem.addEventListener('pointerup', event => {
            this.mouseUpPos.fromArray(this.getMousePos(this.canvasElem, event.clientX, event.clientY));
        });

        this.canvasElem.addEventListener('click', e => {
            if (!this.mouseUpPos.distanceTo(this.mouseDownPos)) {
                const intersects = this.objectSelector.intersectionTest(this.project.scene, this.world.camera, e);

                let intersected = false;

                for (let it of intersects) {
                    if (it.object !== this.selectedObject &&
                        !it.object.isLine &&
                        !it.object.isPoints &&
                        it.object.name != 'ezv-baked-shadow') {

                        const cmd = new SelectObjectCommand(it.object, it);
                        CommandManager.execute(cmd);

                        if (it.object.isLight)
                            this.bindLightHelper(it.object.constructor.name, it.object);

                        EventAggregator.instance.notifyAll(new RayObjectIntersectionEvent(it));

                        intersected = true;

                        break;
                    }
                }

                if (!intersected) {
                    if (this.selectedObject) {
                        const cmd = new DeselectObjectCommand(this.selectedObject);

                        CommandManager.execute(cmd);
                    }
                }
            }

            EventAggregator.instance.notifyAll(new ViewportClickEvent(e));

            const annot = this.annotationLabels.selectTest(e.offsetX, e.offsetY, this.world.camera);
            if (annot) {
                const rot = new Quaternion();
                rot.setFromRotationMatrix(annot.userData.cameraState.matrix);

                const pos = new Vector3();
                pos.setFromMatrixPosition(annot.userData.cameraState.matrix);

                this.cameraAnimation = new CameraAnimation(this.world.cameraController);

                this.orbitCameraController.enabled = false;

                this.cameraAnimation.start(pos, rot, annot.userData.cameraState, () => {
                    this.orbitCameraController.enabled = true;
                });
            }
        });

        this.gradient = createGradient(this.world.camera);
        this.world.scene.add(this.gradient);

        this.reflector = createDepthFadeReflector(this.world, 10,
            this.project.settings.state.environment.reflectionColor, this.project.settings.state.environment.reflectionFadeScale,
            this.project.settings.state.environment.reflectionOpacity, this.project.settings.state.environment.blurReflection);
        this.world.scene.add(this.reflector);

        this.expFog = new FogExp2(0xffffff, this.project.settings.state.environment.fogColor, this.project.settings.state.environment.fogDensity);
        this.fog = new Fog(0xffffff, this.project.settings.state.environment.fogNear, this.project.settings.state.environment.fogFar);

        this.world.scene.fog = this.project.settings.state.environment.fogType === 'linear' ? this.fog : this.expFog;

        this.shadowPlane = createShadowPlane(10, this.project.settings.state.lighting.shadowLightness);
        this.shadowPlane.name = 'ezv-shadow-plane';

        this.world.scene.add(this.shadowPlane);

        this.shadowLight = createShadowLight(10, this.project.settings.state.lighting.shadowLightRotation,
            this.project.settings.state.lighting.shadowLightElevation);
        this.shadowLight.name = 'ezv-shadow-light';

        this.shadowLight.visible = false;

        this.world.scene.add(this.shadowLight);

        //    this.shadowCameraHelper = new CameraHelper(this.shadowLight.shadow.camera);
        //     this.world.scene.add(this.shadowCameraHelper);

        this.annotationLabels = new AnnotationLabels(this.containerElem, this.project.settings.state.annotations);

        this.world.addRenderFrameCallback(dt => {
            this.annotationLabels.updateLabels(this.world.camera);

            this.cameraAnimation.update(dt);

            this.project.scene.traverse(o => {
                if (o.animationPlayer)
                    o.animationPlayer.update(dt);
            })
        });

        this.cameraAnimation = new CameraAnimation(this.orbitCameraController);

        this.setGrid(100, 40, new Color(0x202020), new Color(0x402020), true);

        const elems = document.getElementsByClassName('footer');
        if (elems.length)
            elems[0].style.display = 'none';

        this.lightTexture = new TextureLoader().load(lightHelperImage);

        const light = this.addPointLight();
        light.intensity = 1;

        light.position.set(-2, 2, -2);

        const cube = createBox();

        this.addObject(cube);

        this.bloomPass = this.world.composer.createBloomPass(this.project.settings.state.effects.bloom.threshold,
            this.project.settings.state.effects.bloom.strength,
            this.project.settings.state.effects.bloom.radius);

        this.world.composer.addPass(this.bloomPass);

        this.dofPass = this.world.composer.createDofPass(this.world.scene, this.world.camera,
            this.project.settings.state.effects.dof.focus * this.world.camera.far * 0.1,
            this.project.settings.state.effects.dof.focalLength * 1 / this.world.camera.far,
            this.project.settings.state.effects.dof.blur * 0.01
        );

        this.world.composer.addPass(this.dofPass);

        this.setEffects();

        //  this.sceneBBoxHelper = new BoxHelper(this.project.scene);

        //  this.world.scene.add(this.sceneBBoxHelper);

        this.newProject();

        this.frameObject(this.project.scene);
    }

    newProject() {
        const scene = new Object3D();
        const settings = new EditorSettings();
        const project = new Project(scene, settings);

        this.project = project;

        const cube = createBox();
        cube.position.set(0, 0.5, 0);

        this.addObject(cube);

        this.frameObject(this.project.scene);
    }


    setGrid(size, divisions, color, centerColor, visible) {
        if (this._grid) {
            this._grid.removeFromParent();
            this._grid.dispose();
        }

        this._grid = new GridHelper(size, divisions, centerColor, color);
        this._grid.visible = visible;

        this.world.scene.add(this._grid);
    }

    createOrbitCamera() {
        const orbitCam = new PerspectiveCamera(45.0, this.viewport.aspect, 0.1, 1000);
        orbitCam.name = 'Orbit_Camera';

        return orbitCam;
    }

    createFirstPersonCamera() {
        const fpCam = new PerspectiveCamera(45.0, this.viewport.aspect, 0.1, 1000);
        fpCam.name = 'First_Person_Camera';

        return fpCam;
    }

    bindCamerasToControllers(scene) {
        let orbitCam = scene.getObjectByName('Orbit_Camera');
        if (!orbitCam) {
            orbitCam = this.createOrbitCamera(this.viewport.aspect);
            scene.add(orbitCam);
        }

        this.orbitCameraController.object = orbitCam;
        this.orbitCameraController.saveState();

        let fpCam = scene.getObjectByName('First_Person_Camera');
        if (!fpCam) {
            fpCam = this.createFirstPersonCamera(this.viewport.aspect);
            scene.add(fpCam);
        }

        this.firstPersonCameraController.object = fpCam;
    }

    renderScene() {
        this.world.composer.render(this.world.scene, this.world.camera);
    }

    toggleFullScreen() {
        if (!this.containerElem)
            return;

        if (!document.fullscreenElement) {
            if (this.containerElem.requestFullscreen)
                this.containerElem.requestFullscreen();
            else if (this.containerElem.mozRequestFullScreen)
                this.containerElem.mozRequestFullScreen();
            else if (this.containerElem.msRequestFullscreen)
                this.containerElem.msRequestFullscreen();
            else if (this.containerElem.webkitRequestFullscreen)
                this.containerElem.webkitRequestFullscreen(Element.ALLOW_KEYBOARD_INPUT);
        }
        else {
            if (document.exitFullscreen)
                document.exitFullscreen();
            else if (document.webkitExitFullscreen)
                document.webkitExitFullscreen();
            else if (document.mozCancelFullScreen)
                document.mozCancelFullScreen();
            else if (document.msExitFullscreen)
                document.msExitFullscreen();
        }
    }

    addDepthFadeReflector() {
        if (this.depthFadeReflector) {
            this.depthFadeReflector.removeFromParent();
            this.depthFadeReflector.dispose();
        }

        const boundingGeometry = new BoundingGeometry(this.project.scene);

        this.depthFadeReflector = createDepthFadeReflector(
            this.world,
            boundingGeometry,
            this.project.settings.state.environment.reflectionColor,
            this.project.settings.state.environment.reflectionFadeScale,
            this.project.settings.state.environment.reflectionOpacity,
            this.project.settings.state.environment.blurReflection);

        this.world.scene.add(this.depthFadeReflector);
    }

    deleteBakedShadowMesh(object) {
        if (object.bakedShadow) {
            object.bakedShadow.mesh.removeFromParent();

            object.bakedShadow.dispose();
            object.bakedShadow = null;
        }
    }

    renderBakedShadow(object) {
        const lightness = object.userData.bakedShadowLightness ?? 0.7;
        const blur = object.userData.bakedShadowBlur ?? 0.7;
        const falloff = object.userData.bakedShadowFalloff ?? 0;

        this.deleteBakedShadowMesh(object);

        object.bakedShadow = createBakedShadowPlane(object, this.world.renderer, lightness, blur, falloff);

        object.attach(object.bakedShadow.mesh);
        object.bakedShadow.mesh.name = 'ezv-baked-shadow';
    }

    removeBakedShadowFromObject(object) {
        this.deleteBakedShadowMesh(object);

        delete object.userData.bakedShadowLightness;
        delete object.userData.bakedShadowBlur;
        delete object.userData.bakedShadowFalloff;
    }

    getBakedShadowProperties(object) {
        return {
            bakedShadowBlur: object.userData.bakedShadowBlur ?? 0.7,
            bakedShadowLightness: object.userData.bakedShadowLightness ?? 0.7,
            bakedShadowFalloff: object.userData.bakedShadowFalloff ?? 0,
        };
    }

    setBakedShadowProperties(object, props) {
        object.userData.bakedShadowBlur = props.bakedShadowBlur;
        object.userData.bakedShadowLightness = props.bakedShadowLightness;
        object.userData.bakedShadowFalloff = props.bakedShadowFalloff;

        this.renderBakedShadow(object, object.userData.bakedShadowLightness, object.userData.bakedShadowBlur,
            object.userData.bakedShadowFalloff);
    }

    createAllBakedShadows() {
        this.project.scene.traverse(object => {
            if (object.userData.bakedShadowBlur != undefined) {
                this.renderBakedShadow(object);
            }
        });
    }

    deleteAllBakedShadowMeshes() {
        this.project.scene.traverse(o => {
            if (o.bakedShadow) {
                this.deleteBakedShadowMesh(o);
            }
        });
    }

    addObject(object, parent) {
        if (!object)
            return;

        if (parent)
            parent.add(object);
        else
            this.project.scene.add(object);

        object.traverse(o => {
            if (o instanceof Mesh) {
                o.receiveShadow = true;
                o.castShadow = true;
            }

            if (o?.animations?.length) {
                o.animationPlayer = new AnimationPlayer(o, o.animations);
            }
        });

        this.recalculateSceneBounds();

        EventAggregator.instance.notifyAll(new SceneModifiedEvent());
    }

    removeObject(object) {
        if (object.isCamera)
            return;

        object.removeFromParent();

        if (object.isLight && object.helper)
            this.removeLightHelper(object.helper);

        const tracker = new ResourceTracker();

        tracker.track(object);
        tracker.dispose();

        this.deselectAll();

        EventAggregator.instance.notifyAll(new SceneModifiedEvent());
    }

    updateLight(light) {
        if (light instanceof SpotLight || light instanceof DirectionalLight) {
            light.helper && light.helper.update();

            if (light.target) {
                light.matrix.extractBasis(v1, v2, v3);

                light.target.position.set(0, 0, 0);
                light.target.position.addScaledVector(v3, 2);
            }
        }
    }

    addPointLight() {
        const light = new PointLight();

        this.addObject(light);

        let helper = new PointLightHelper(light, this.lightTexture);

        light.helper = helper;

        this.world.scene.add(helper);

        return light;
    }

    addDirectionalLight() {
        const light = new DirectionalLight();

        this.addObject(light);
        light.add(light.target);

        let helper = new DirectionalLightHelper(light, this.lightTexture);

        light.helper = helper;

        this.world.scene.add(helper);

        return light;
    }

    addSpotLight() {
        const light = new SpotLight();

        this.addObject(light);
        light.add(light.target);

        let helper = new SpotLightHelper(light, this.lightTexture);

        light.helper = helper;

        this.world.scene.add(helper);

        return light;
    }

    addHemisphereLight() {
        const light = new HemisphereLight();

        this.addObject(light);

        let helper = new PointLightHelper(light, this.lightTexture);

        light.helper = helper;

        this.world.scene.add(helper);

        return light;
    }

    addLightHelpers() {
        this.project.scene.traverse(o => {
            if (o.isLight) {
                if (o.helper) {
                    o.helper.removeFromParent();

                    o.dispose && o.dispose();
                }

                let helper;

                if (o instanceof PointLight)
                    helper = new PointLightHelper(o, this.lightTexture);
                else if (o instanceof DirectionalLight)
                    helper = new DirectionalLightHelper(o, this.lightTexture);
                else if (o instanceof SpotLight)
                    helper = new SpotLightHelper(o, this.lightTexture);
                else if (o instanceof HemisphereLight)
                    helper = new PointLightHelper(o, this.lightTexture);

                if (helper) {
                    o.helper = helper;

                    this.world.scene.add(helper);

                    o.helper.update();
                }
            }
        })
    }

    removeLightHelper(object) {
        if (object.isLightHelper) {
            object.removeFromParent();

            const tracker = new ResourceTracker();

            tracker.track(object);
            tracker.dispose();
        }
    }

    getLightHelpers() {
        const helpers = [];

        this.world.scene.traverse(o => {
            if (o.isLightHelper)
                helpers.push(o);
        });

        return helpers;
    }

    removeLightHelpers() {
        this.getLightHelpers().forEach(o => this.removeLightHelper(o));
    }

    frameObject(object) {
        const bg = new BoundingGeometry(object);
        const fov = this.orbitCameraController.object.fov;
        let d;

        if (this.orbitCameraController.object.aspect > 1)
            d = (bg.boundingSphere.radius) / Math.tan((fov / 2) * Math.PI / 180);
        else
            d = (bg.boundingSphere.radius / this.orbitCameraController.object.aspect) / Math.tan((fov / 2) * Math.PI / 180);

        const camX = bg.center.x + 1 * d;
        const camY = bg.center.y + 1 * d;
        const camZ = bg.center.z + 1 * d;

        this.orbitCameraController.object.position.set(camX, camY, camZ);
        this.orbitCameraController.target.copy(bg.center);
        this.orbitCameraController.clearPanOffset();
        this.orbitCameraController.update();
    }

    frameScene() {
        this.frameObject(this.project.scene);

        const bg = new BoundingGeometry(this.project.scene);

        this.orbitCameraController.object.near = bg.boundingSphere.radius * 0.01;
        this.orbitCameraController.object.far = bg.boundingSphere.radius * 100;
        this.orbitCameraController.object.updateProjectionMatrix();
    }

    recalculateSceneBounds() {
        const sceneBg = new BoundingGeometry(this.world.sceneRoot);
        const r = sceneBg.boundingSphere.radius;

        this.shadowLight.shadow.camera.near = r * 0.01;
        this.shadowLight.shadow.camera.far = r * 10;
        this.shadowLight.shadow.camera.left = -r * 2;
        this.shadowLight.shadow.camera.right = r * 2;
        this.shadowLight.shadow.camera.top = r * 2;
        this.shadowLight.shadow.camera.bottom = -r * 2;

        this.shadowLight.shadow.camera.updateProjectionMatrix();

        const x = r * Math.sin(this.project.settings.state.lighting.shadowLightElevation) * Math.cos(this.project.settings.state.lighting.shadowLightRotation);
        const z = r * Math.sin(this.project.settings.state.lighting.shadowLightElevation) * Math.sin(this.project.settings.state.lighting.shadowLightRotation);
        const y = r * Math.cos(this.project.settings.state.lighting.shadowLightElevation);

        this.shadowLight.position.set(sceneBg.boundingSphere.center.x + x, sceneBg.boundingSphere.center.y + y, sceneBg.boundingSphere.center.z + z);

        v1.set(sceneBg.boundingSphere.center.x, sceneBg.boundingSphere.center.y, sceneBg.boundingSphere.center.z);

        this.shadowLight.lookAt(v1);

        const wr2 = (sceneBg.boundingSphere.center.length() + r) * 2;

        this.shadowPlane.scale.set(wr2, wr2, wr2);

        this.reflector.scale.set(wr2, wr2, wr2);

        this.sceneBBoxHelper && this.sceneBBoxHelper.update();

        this.shadowCameraHelper && this.shadowCameraHelper.update();
    }

    selectObject(object, intersect) {
        if (this.selectedObject === object)
            return;

        this._selectedObject = object;

        this.transformControls.controls.attach(object);

        EventAggregator.instance.notifyAll(new ObjectSelectedEvent(object, intersect));
    }

    setObjectPosition(object, pos) {
        object.position.copy(pos);

        this.recalculateSceneBounds();

        EventAggregator.instance.notifyAll(new ObjectPositionChangedEvent(object));
    }

    setObjectRotation(object, rot) {
        object.rotation.copy(rot);

        this.recalculateSceneBounds();

        EventAggregator.instance.notifyAll(new ObjectRotationChangedEvent(object));
    }

    setObjectScale(object, scale) {
        object.scale.copy(scale);

        this.recalculateSceneBounds();

        EventAggregator.instance.notifyAll(new ObjectScaleChangedEvent(object));
    }

    deselectAll() {
        if (!this.selectedObject)
            return;

        EventAggregator.instance.notifyAll(new ObjectDeseletedEvent(this.selectedObject));

        this._selectedObject = null;

        this.transformControls.controls.detach();
    }

    setObjectTransformMode(mode) {
        this.transformControls.controls.setMode(mode);
    }

    getObjectTransformMode() {
        return this.transformControls.controls.getMode();
    }

    setEnvironment() {
        if (this.envPreviewMode) {
            const settings = this.project.settings.state.environment;

            switch (settings.backgroundType) {
                case 'transparent':
                    this.world.scene.background = null;

                    this.gradient.visible = false;
                    break;
                case 'color':
                    this.world.scene.background = new Color(settings.backgroundColor);

                    this.gradient.visible = false;
                    break;
                case 'gradient':
                    this.gradient.visible = true;

                    col1.set(settings.backgroundGradientColor1)
                    col2.set(settings.backgroundGradientColor2)

                    this.gradient.style({ colors: [col1, col2] });
                    break;
            }

            this.reflector.setFadeScale(settings.reflectionFadeScale);

            col1.set(settings.reflectionColor);

            this.reflector.setColor(col1);
            this.reflector.setOpacity(settings.reflectionOpacity);
            this.reflector.enableBlur(settings.blurReflection);
            this.reflector.visible = settings.enableReflection;

            this.fog.near = settings.fogNear;
            this.fog.far = settings.fogFar;
            this.fog.color.set(settings.fogColor);

            this.expFog.color.set(settings.fogColor);
            this.expFog.density = settings.fogDensity;

            if (!settings.fog)
                this.world.scene.fog = null;
            else
                this.world.scene.fog = settings.fogType === 'linear' ? this.fog : this.expFog;

        }
        else {
            this.world.scene.background = new Color(0x404040);
            this.gradient.visible = false;
            this.reflector.visible = false;
            this.world.scene.fog = null;
        }
    }

    setLighting() {
        const settings = this.project.settings.state.lighting;

        this.shadowLight.castShadow = settings.shadowsEnabled;
        this.shadowLight.intensity = settings.shadowLightIntensity;
        this.shadowLight.visible = settings.shadowsEnabled;
        this.shadowLight.shadow.bias = settings.shadowBias;

        this.shadowPlane.visible = settings.shadowsEnabled && settings.shadowPlaneVisible;
        this.shadowPlane.material.opacity = 1.0 - settings.shadowPlaneLightness;

        this.ambientLight.intensity = settings.ambientLightIntensity;
        this.ambientLight.color.set(settings.ambientLightColor);

        this.recalculateSceneBounds();
    }

    setEffects() {
        if (this.effectsPreviewMode) {
            this.world.composer.enablePass(this.bloomPass, this.project.settings.state.effects.bloom.enabled);
            this.bloomPass.threshold = this.project.settings.state.effects.bloom.threshold;
            this.bloomPass.strength = this.project.settings.state.effects.bloom.strength;
            this.bloomPass.radius = this.project.settings.state.effects.bloom.radius;

            this.world.composer.enablePass(this.dofPass, this.project.settings.state.effects.dof.enabled);
            this.dofPass.uniforms["aperture"].value = this.project.settings.state.effects.dof.focalLength * 1 / this.world.camera.far;
            this.dofPass.uniforms["focus"].value = this.project.settings.state.effects.dof.focus * this.world.camera.far * 0.1;
            this.dofPass.uniforms["maxblur"].value = this.project.settings.state.effects.dof.blur * 0.02;
        }
        else {
            this.world.composer.enablePass(this.bloomPass, false);
            this.world.composer.enablePass(this.dofPass, false);
        }
    }

    setCameraControllerType(type) {
        if (type == 'orbit') {
            this.cameraController = this.orbitCameraController;
            this.firstPersonCameraController.enabled = false;
            //      this.setState({ showVirtualJoystick: false });
        }
        else {
            this.cameraController = this.firstPersonCameraController;
            this.orbitCameraController.enabled = false;
            //     this.setState({ showVirtualJoystick: true });
        }

        this.world.cameraController = this.cameraController;

        this.cameraController.enabled = true;
    }

    setCameraController() {
        const settings = this.project.settings.state.cameraController;

        this.setCameraControllerType(settings.controllerType);

//        this.orbitCameraController.minDistance = Number(settings.orbitController.minZoom);
//        this.orbitCameraController.maxDistance = Number(settings.orbitController.maxZoom);
        this.orbitCameraController.minPolarAngle = Number(settings.orbitController.minElevation) * Math.PI / 180.0;
        this.orbitCameraController.maxPolarAngle = Number(settings.orbitController.maxElevation) * Math.PI / 180.0;

        this.firstPersonCameraController.movementSpeed = Number(settings.firstPersonController.moveSpeed);

        this.orbitCameraController.update();
    }

    addAnnotation(object, parent) {
        parent.add(object);

        EventAggregator.instance.notifyAll(new SceneModifiedEvent());
    }

    removeAnnotation(object) {
        object.geometry.dispose();
        object.material.dispose();
        object.removeFromParent();
    }

    updateAnnotations() {
        const settings = this.project.settings.state.annotations;

        this.project.scene.traverse(o => {
            if (o instanceof Points) {
                updateAnnotation(o, settings.size, o.userData.buttonText, o.userData.labelText, settings.fillColor, settings.textColor, settings.borderColor, o.userData.cameraState);
            }
        });
    }
}

export const Editor = new _Editor();