import Command from "../command";
import { Editor } from "../editor";

export default class SetBakedShadowCommand extends Command {
    constructor(object, props) {
        super();

        this.object = object;
        this.props = props;
    }

    get name() {
        return 'Set Baked Shadow';
    }

    execute() {
        Editor.setBakedShadowProperties(this.object, this.props);
    }

    undo() {
    }

    redo() {
        this.execute();
    }
}
