import React from 'react';
import PropTypes from 'prop-types';
import TextField from '@material-ui/core/TextField';
import MaterialView from './materialView';
import CameraSettingsView from './cameraSettingsView';
import AnnotationView from './annotationView';
import Divider from '@material-ui/core/Divider';
import AnimationsView from './animationsView';
import LightSettingsView from './lightSettingsView';

export default class Object3dPropertiesView extends React.Component {
    constructor() {
        super();

        this.state = {};
    }

    render() {
        if (!this.props.viewModel || Object.keys(this.state).length === 0 || !this.props.viewModel.object)
            return;

        return (
            <div>
                <div className='editor-pane'>
                    <div className='editor-sidebar-heading'>
                        {this.state.type + ' '} Properties
                    </div>
                    <div className='flexrow'>
                        Name
                    </div>
                    <TextField
                        value={this.state.name}
                        style={{ width: '250px', marginRight: '2px' }}
                        onChange={e => this.props.viewModel.onNameChanged(e, 'x')} />
                    <div className='flexrow'>
                        Position
                    </div>
                    <div className='flexrow'>
                        <div>
                            x&nbsp;
                            <TextField
                                value={this.state.xPos}
                                type='number'
                                style={{ width: '100px', marginRight: '2px' }}
                                onChange={e => this.props.viewModel.onPositionChanged(e, 'x')}
                                onBlur={e => this.props.viewModel.onPositionBlur(e, 'x')}
                                onKeyUp={e => this.props.viewModel.onPositionBlur(e, 'x')} />
                        </div>
                        <div>
                            y&nbsp;
                            <TextField
                                value={this.state.yPos}
                                type='number'
                                style={{ width: '100px', marginRight: '2px' }}
                                onChange={e => this.props.viewModel.onPositionChanged(e, 'y')}
                                onBlur={e => this.props.viewModel.onPositionBlur(e, 'y')}
                                onKeyUp={e => this.props.viewModel.onPositionBlur(e, 'y')} />
                        </div>
                        <div>
                            z&nbsp;
                            <TextField
                                value={this.state.zPos}
                                type='number'
                                style={{ width: '100px', marginRight: '2px' }}
                                onChange={e => this.props.viewModel.onPositionChanged(e, 'z')}
                                onBlur={e => this.props.viewModel.onPositionBlur(e, 'z')}
                                onKeyUp={e => this.props.viewModel.onPositionBlur(e, 'z')} />
                        </div>
                    </div>
                    <div className='flexrow'>
                        Rotation
                    </div>
                    <div className='flexrow'>
                        <div>
                            x&nbsp;
                            <TextField
                                value={this.state.xRot}
                                type='number'
                                style={{ width: '100px', marginRight: '2px' }}
                                onChange={e => this.props.viewModel.onRotationChanged(e, 'x')}
                                onBlur={e => this.props.viewModel.onRotationBlur(e, 'x')}
                                onKeyUp={e => this.props.viewModel.onRotationBlur(e, 'x')} />
                        </div>
                        <div>
                            y&nbsp;
                            <TextField
                                value={this.state.yRot}
                                type='number'
                                style={{ width: '100px', marginRight: '2px' }}
                                onChange={e => this.props.viewModel.onRotationChanged(e, 'y')}
                                onBlur={e => this.props.viewModel.onRotationBlur(e, 'y')}
                                onKeyUp={e => this.props.viewModel.onRotationBlur(e, 'y')} />
                        </div>
                        <div>
                            z&nbsp;
                            <TextField
                                value={this.state.zRot}
                                type='number'
                                style={{ width: '100px', marginRight: '2px' }}
                                onChange={e => this.props.viewModel.onRotationChanged(e, 'z')}
                                onBlur={e => this.props.viewModel.onRotationBlur(e, 'z')}
                                onKeyUp={e => this.props.viewModel.onRotationBlur(e, 'z')} />
                        </div>
                    </div>
                    <div className='flexrow'>
                        Scale
                    </div>
                    <div className='flexrow'>
                        <div>
                            x&nbsp;
                            <TextField
                                value={this.state.xScale}
                                type='number'
                                style={{ width: '100px', marginRight: '2px' }}
                                onChange={e => this.props.viewModel.onScaleChanged(e, 'x')}
                                onBlur={e => this.props.viewModel.onScaleBlur(e, 'x')}
                                onKeyUp={e => this.props.viewModel.onScaleBlur(e, 'x')} />
                        </div>
                        <div>
                            y&nbsp;
                            <TextField
                                value={this.state.yScale}
                                type='number'
                                style={{ width: '100px', marginRight: '2px' }}
                                onChange={e => this.props.viewModel.onScaleChanged(e, 'y')}
                                onBlur={e => this.props.viewModel.onScaleBlur(e, 'y')}
                                onKeyUp={e => this.props.viewModel.onScaleBlur(e, 'y')} />
                        </div>
                        <div>
                            z&nbsp;
                            <TextField
                                value={this.state.zScale}
                                type='number'
                                style={{ width: '100px', marginRight: '2px' }}
                                onChange={e => this.props.viewModel.onScaleChanged(e, 'z')}
                                onBlur={e => this.props.viewModel.onScaleBlur(e, 'z')}
                                onKeyUp={e => this.props.viewModel.onScaleBlur(e, 'z')} />
                        </div>
                    </div>
                    <Divider />
                    <br />
                    {
                        (this.state.type === 'Mesh' && this.props.viewModel.materialViewModel) &&
                        <div className='flexrow'>
                            <div className='editor-sidebar-heading'>
                                Material
                            </div>
                            <MaterialView
                                ref={this.props.viewModel.materialViewModel.updateViewState}
                                viewModel={this.props.viewModel.materialViewModel} />
                        </div>
                    }
                    {
                        this.state.type === 'Camera' &&
                        <CameraSettingsView
                            ref={this.props.viewModel.cameraViewModel.updateViewState}
                            viewModel={this.props.viewModel.cameraViewModel} />
                    }
                    {
                        this.state.type === 'Points' &&
                        <AnnotationView
                            ref={this.props.viewModel.annotationViewModel.updateViewState}
                            viewModel={this.props.viewModel.annotationViewModel} />
                    }
                    {
                        this.state.type === 'Light' &&
                        <LightSettingsView
                            ref={this.props.viewModel.lightViewModel.updateViewState}
                            viewModel={this.props.viewModel.lightViewModel} />
                    }
                    <AnimationsView ref={this.props.viewModel.animationsViewModel.updateViewState}
                        viewModel={this.props.viewModel.animationsViewModel} />
                </div>
            </div>
        )
    }
}

Object3dPropertiesView.propTypes = {
    viewModel: PropTypes.object,
};  