import EditorTab from "members/features/editor/editorTab";
import Tooltip from '@material-ui/core/Tooltip';
import React from "react";
import { Fragment } from "react";
import CameraAlt from '@material-ui/icons/CameraAlt';
import CameraControllerSettingsView from "../views/cameraControllerSettingsView";
import CameraControllerSettingsViewModel from "../viewModels/cameraControllerSettingsViewModel";

export default class CameraControllerTab extends EditorTab {
    constructor() {
        super();

        this.viewModel = new CameraControllerSettingsViewModel();
    }

    renderButtonIcon() {
        return (
            <CameraAlt />
        )
    }

    renderContent() {
        return (
            <Fragment>
                <div className='editor-sidebar-heading' style={{ fontSize: 'large' }}>
                    Camera Controller Properties
                </div>
                <CameraControllerSettingsView viewModel={this.viewModel} ref={this.viewModel.updateViewState} />
            </Fragment>
        );
    }
}