import React, { Fragment } from "react";
import PropTypes from 'prop-types';
import FileMenu from "./fileMenu";
import EditMenu from "./editMenu";
import AddMenu from "./addMenu";
import ViewMenu from "./viewMenu";

export default class EditorMenu extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            fileAnchorElem: null,
            editAnchorElem: null,
            addAnchorElem: null,
            viewAnchorElem: null
        };

        this.onFileMenuClick = this.onFileMenuClick.bind(this);
        this.onEditMenuClick = this.onEditMenuClick.bind(this);
        this.onAddMenuClick = this.onAddMenuClick.bind(this);
        this.onViewMenuClick = this.onViewMenuClick.bind(this);
    }

    render() {
        return (
            <Fragment>
                <div className='main-menu'>
                    <div onClick={this.onFileMenuClick}>
                        File
                    </div>
                    <div onClick={this.onEditMenuClick}>
                        Edit
                    </div>
                    <div onClick={this.onAddMenuClick}>
                        Add
                    </div>
                    <div onClick={this.onViewMenuClick}>
                        View
                    </div>
                    <div>
                        <a href='/help/editing' target='_blank'>
                            Help
                        </a>
                    </div>
                </div>
                <FileMenu
                    anchorElem={this.state.fileAnchorElem}
                    disabled={false}
                    onClose={() => { this.setState({ fileAnchorElem: null }) }}
                    fileCommands={this.props.fileCommands} 
                    editorViewModel={this.props.editorViewModel} />
                <EditMenu
                    anchorElem={this.state.editAnchorElem}
                    disabled={false}
                    onClose={() => { this.setState({ editAnchorElem: null }) }}
                    editCommands={this.props.editCommands} />
                <AddMenu
                    anchorElem={this.state.addAnchorElem}
                    disabled={false}
                    onClose={() => { this.setState({ addAnchorElem: null }) }}
                    addCommands={this.props.addCommands} />
                <ViewMenu
                    anchorElem={this.state.viewAnchorElem}
                    disabled={false}
                    onClose={() => { this.setState({ viewAnchorElem: null }) }}
                    viewCommands={this.props.viewCommands} />
            </Fragment>
        );
    }

    onFileMenuClick(e) {
        this.setState({ fileAnchorElem: e.currentTarget });
    }

    onEditMenuClick(e) {
        this.setState({ editAnchorElem: e.currentTarget });
    }

    onAddMenuClick(e) {
        this.setState({ addAnchorElem: e.currentTarget });
    }
    onViewMenuClick(e) {
        this.setState({ viewAnchorElem: e.currentTarget });
    }
}

EditorMenu.propTypes = {
    fileCommands: PropTypes.object,
    editCommands: PropTypes.object,
    addCommands: PropTypes.object,
    viewCommands: PropTypes.object,
    editorViewModel: PropTypes.object
}