import Command from "../command";
import { Editor } from "../editor";

export default class SelectObjectCommand extends Command {
    constructor(object, intersect) {
        super();
        
        this.object = object;
        this.intersect = intersect;
    }

    get name() {
        return 'Select Object';
    }

    execute() {
        Editor.selectObject(this.object, this.intersect);
    }

    undo() {
        Editor.deselectAll();
    }

    redo() {
        Editor.selectObject(this.object);
    }
}