import EventAggregator from "ezyvuecore/core/events/eventAggregator";
import { UndoRedoStatusChanged } from "./editorEvents";

class _CommandManager {
    constructor() {
        this.redoStack = [];
        this.undoStack = [];
    }

    async execute(cmd, commit = true) {
        await cmd.execute();

        if (commit) {
            this.redoStack = [];

            if (cmd.undoable)
                this.undoStack.push(cmd);

            EventAggregator.instance.notifyAll(new UndoRedoStatusChanged());
        }
    }

    undo() {
        if (!this.undoStack.length)
            return;

        const cmd = this.undoStack.pop();
        cmd.undo();
        this.redoStack.push(cmd);

        EventAggregator.instance.notifyAll(new UndoRedoStatusChanged());
    }

    redo() {
        if (!this.redoStack.length)
            return;

        const cmd = this.redoStack.pop();
        cmd.redo();
        this.undoStack.push(cmd);

        EventAggregator.instance.notifyAll(new UndoRedoStatusChanged());
    }

    get nextRedoCommand() {
        if (!this.redoStack.length)
            return null;

        return this.redoStack.slice(-1).pop();
    }

    get nextUndoCommand() {
        if (!this.undoStack.length)
            return null;

        return this.undoStack.slice(-1).pop();
    }
}

export const CommandManager = new _CommandManager();