import React, { Fragment } from "react";
import PropTypes from 'prop-types';

export default class EditorTabPanel extends React.Component {
    constructor() {
        super();

        this.state = {
            activeTabIndex: 0
        };
    }

    render() {
        return (
            <Fragment>
                <div className="tab">
                    {
                        this.props.tabs.map((tab, index) => {
                            return tab.renderButton(this.state.activeTabIndex == index, () => this.onTabButtonClick(tab, index), index)
                        })
                    }
                </div>
                <Fragment>
                    {
                        this.props.tabs.length > 0 &&
                        this.props.tabs[this.state.activeTabIndex].renderContent()
                    }
                </Fragment>
            </Fragment>
        )
    }

    onTabButtonClick(tab, index) {
        this.setState({ activeTabIndex: index }, () => {
            this.props.onTabChanged && this.props.onTabChanged(tab, index);
        });
    }
}

EditorTabPanel.propTypes = {
    tabs: PropTypes.array,
    onTabChanged: PropTypes.func
}