import EventAggregator from "ezyvuecore/core/events/eventAggregator";
import { ProjectChangedEvent, ProjectSettingsChangedEvent } from "../editorEvents";
import { CommandManager } from "../commandManager";
import SetEnvironmentCommand from "../commands/setEnvironmentCommand";
import SetEnvironmentMapCommand from "../commands/setEnvironmentMapCommand";
import { Editor } from "../editor";
import { cloneDeep } from "lodash";

export default class EnvironmentSettingsViewModel {
    constructor() {
        this.onEnvMapChanged = this.onEnvMapChanged.bind(this);
        this.onEnvMapDelete = this.onEnvMapDelete.bind(this);
        this.updateViewState = this.updateViewState.bind(this);
        this.setProperty = this.setProperty.bind(this);
        this.onViewStateChanged = this.onViewStateChanged.bind(this);
        
        EventAggregator.instance.subscribe(ProjectSettingsChangedEvent, () => {
            this.updateViewState();
        });

        EventAggregator.instance.subscribe(ProjectChangedEvent, () => {
            this.updateViewState();
        });

        this.setFromSettings();

        this.viewState = {
            preview: false
        }
    }

    setFromSettings() {
        Editor.setEnvironment(Editor.project.settings.state.environment);
    }
    
    onEnvMapChanged(texture) {
        CommandManager.execute(new SetEnvironmentMapCommand(texture));
    
        this.updateViewState();
    }

    onEnvMapDelete() {
        CommandManager.execute(new SetEnvironmentMapCommand(null));

        this.updateViewState();
    }

    setProperty(prop, value) {
        const state = cloneDeep(Editor.project.settings.state.environment);
        state[prop] = value;

        const cmd = new SetEnvironmentCommand(state, Editor.project.settings.state.environment);

        CommandManager.execute(cmd);

        this.updateViewState();
    }
        
    onViewStateChanged(name, value) {
        this.viewState[name] = value;

        Editor.envPreviewMode = this.viewState.preview;

        this.updateViewState();
    }

    updateViewState(view = null) {
        if (view)
            this.view = view;

        if (this.view) {
            this.view.setState({
                envMap: Editor.world.scene.environment,
                settings: Editor.project.settings.state.environment,
                viewState: this.viewState
            });
        }
    }
}