import { TransformControls } from "ezyvuecore/three/examples/jsm/controls/TransformControls";
import { CommandManager } from "../commandManager";
import SetPositionCommand from "../commands/setPositionCommand";
import SetRotationCommand from "../commands/setRotationCommand";
import SetScaleCommand from "../commands/setScaleCommand";

export default class ObjectTransformControls {
    get controls() {
        return this._transformControls;
    }

    set camera(camera) {
        this._transformControls.camera = camera;
    }

    get object() {
        return this._transformControls.object;
    }

    constructor(camera, domNode) {
        this._transformControls = new TransformControls(camera, domNode);

        // this._transformControls.addEventListener('change', () => { });

        this._transformControls.addEventListener('dragging-changed', event => {
            this.onDragChanged && this.onDragChanged(event);
        });

        this._transformControls.addEventListener('mouseDown', () => {
            const object = this._transformControls.object;
            if (object) {
                this.initialObjectPos = object.position.clone();
                this.initialObjectRot = object.rotation.clone();
                this.initialObjectScale = object.scale.clone();
            }
        });

        this._transformControls.addEventListener('mouseUp', () => {
            const object = this._transformControls.object;
            if (object) {
                switch (this._transformControls.getMode()) {
                    case 'translate':
                        if (!this.initialObjectPos.equals(object.position)) {
                            const cmd = new SetPositionCommand(object, object.position.clone(), this.initialObjectPos.clone());
                            CommandManager.execute(cmd);
                        }
                        break;

                    case 'rotate':
                         if (!this.initialObjectRot.equals(object.rotation)) {
                            const cmd = new SetRotationCommand(object, object.rotation, this.initialObjectRot);
                            CommandManager.execute(cmd);
                        }
                        break;

                    case 'scale':
                        if (!this.initialObjectScale.equals(object.scale)) {
                            const cmd = new SetScaleCommand(object, object.scale, this.initialObjectScale);
                            CommandManager.execute(cmd);
                         }
                        break;

                }
            }
        });
    }
}