import { Mesh, MeshBasicMaterial, OrthographicCamera, PlaneGeometry, Scene, WebGLRenderer } from "ezyvuecore/three/build/three";
import React from "react";
import PropTypes from 'prop-types';

export default class TextureCanvas extends React.Component {
    constructor() {
        super();

        this.canvasRef = React.createRef();
    }

    componentDidUpdate(prevProps) {
        if (this.props.texture !== prevProps.texture && this.canvasRef.current) {
            this.drawImage(this.props.texture);
        }
    }

    componentDidMount() {
        this.props.texture && this.drawImage(this.props.texture);
    }

    renderToCanvas(texture) {
        const renderer = new WebGLRenderer();

        renderer.setSize(texture.image.width, texture.image.height, false);

        const scene = new Scene();
        const camera = new OrthographicCamera(- 1, 1, 1, - 1, 0, 1);

        const material = new MeshBasicMaterial({ map: texture });
        const quad = new PlaneGeometry(2, 2);
        const mesh = new Mesh(quad, material);

        scene.add(mesh);

        renderer.render(scene, camera);

        return renderer.domElement;
    }

    drawImage(texture) {
        this.texture = texture;

        const context = this.canvasRef.current.getContext('2d');
        if (context) {
            context.clearRect(0, 0, this.canvasRef.current.width, this.canvasRef.current.height);

            if (texture) {
                const image = texture.image;

                if (image && image.width > 0) {
                    const scale = this.canvasRef.current.width / image.width;

                    if (image.data === undefined) {
                        context.drawImage(image, 0, 0, image.width * scale, image.height * scale);
                    }
                    else {
                        const canvas = this.renderToCanvas(texture);
                        context.drawImage(canvas, 0, 0, image.width * scale, image.height * scale);
                    }
                }
            }
        }
    }

    render() {
        return (
            <canvas ref={this.canvasRef} onClick={this.props.onClick} style={{
                width: '50px',
                height: '50px',
                border: '1px solid #000',
                margin: '2px',
                display: 'block',
            }} />
        );
    }
}

TextureCanvas.propTypes = {
    texture: PropTypes.object,
    onClick: PropTypes.func
}