import Command from "../command";
import { Editor } from "../editor";

export default class DeselectObjectCommand extends Command {
    constructor(object) {
        super();
        
        this.object = object;
    }

    get name() {
        return 'Deselect Object';
    }

    execute() {
        Editor.deselectAll();
    }

    undo() {
        Editor.selectObject(this.object);
    }

    redo() {
        this.execute();
    }
}