import { unzipSync } from 'fflate';

export async function loadZipFileFromUrl(url) {
    const resp = await fetch(url);
    if(!resp.ok)
        throw new Error('Failed to load zip file');

    const buff = await resp.arrayBuffer();

    const files = unzipSync(new Uint8Array(buff));

    return files;
}

export async function loadZipFile(file, onLoad) {
    const reader = new FileReader();

    reader.addEventListener('load', async event => {
        const files = unzipSync(new Uint8Array(event.target.result));

        onLoad(files);
    });

    reader.readAsArrayBuffer(file);
}
