import { GLTFExporter } from "ezyvuecore/three/examples/jsm/exporters/GLTFExporter";
import Command from "../command";
import { Editor } from "../editor";

export default class ExportGlbCommand extends Command {
	constructor(fn = 'scene.glb') {
		super();

		this.fn = fn;
	}

    getAnimations() {
		const animations = [];

		Editor.world.sceneRoot.traverse( object => {
			animations.push( ... object.animations );
		});

		return animations;
	}

    save( blob, filename ) {
        const link = document.createElement( 'a' );

		link.href = URL.createObjectURL( blob );
		link.download = filename || 'data.json';
		link.dispatchEvent( new MouseEvent( 'click' ) );
	}

    execute() {
        Editor.deleteAllBakedShadowMeshes();

		const animations = this.getAnimations();

		const exporter = new GLTFExporter();

		exporter.parse(Editor.world.sceneRoot, result => {
			this.save( new Blob( [ result ], { type: 'application/octet-stream' } ), this.fn );
		}, 
        undefined, 
        { binary: true, animations: animations } );

        Editor.createAllBakedShadows();
    }

    get undoable() { return false; }

}