import Event from 'ezyvuecore/core/events/event';

export class RenderRequestEvent extends Event {
}

export class ClickEvent extends Event {
    constructor(type, data) {
        super();
        this.type = type;
        this.data = data;
    }
}

export class ObjectSelectionEvent extends Event {
    constructor(selected, type, object, selectPoint, face) {
        super();
        this.selected = selected;
        this.type = type;
        this.object = object;
        this.point = selectPoint;
        this.face = face;
    }
}

export class ObjectAddedEvent extends Event {
    constructor(object) {
        super();
        this.object = object;
    }
}

export class ObjectDeletedEvent extends Event {
    constructor(object) {
        super();
        this.object = object;
    }
}

export class ObjectMoveEvent extends Event {
    constructor(object) {
        super();
        this.object = object;
    }
}

export class ObjectRotateEvent extends Event {
    constructor(object) {
        super();
        this.object = object;
    }
}

export class DeactivateToolsEvent extends Event {
}

export class AddFlexyBoxCommandEvent extends Event {
    constructor(box) {
        super();
        this.box = box;
    }
}

export class FlexyBoxSelectionEvent extends Event {
    constructor(list, box, select) {
        super();
        this.list = list;
        this.select = select;
        this.box = box;
    }
}

export class FlexyBoxXAlignmentChangeEvent extends Event {
    constructor(box, alignment) {
        super();
        this.box = box;
        this.alignment = alignment;
    }
}

export class FlexyBoxYAlignmentChangeEvent extends Event {
    constructor(box, alignment) {
        super();
        this.box = box;
        this.alignment = alignment;
    }
}

export class AlertEvent extends Event {
    constructor(message) {
        super();
        this.message = message;
    }
}

export class ErrorEvent extends Event {
    constructor(message) {
        super();
        this.message = message;
    }
}
