import { CommandManager } from "../commandManager";
import ExportGlbCommand from "../commands/exportGlbCommand";
import ExportProjectCommand from "../commands/exportProjectCommands";
import ImportDirCommand from "../commands/importDirCommand";
import ImportFileCommand from "../commands/importFileCommand";
import ImportProjectCommand from "../commands/importProjectCommand";
import NewProjectCommand from "../commands/newProjectCommand";
import PublishProjectCommand from "../commands/publishProjectCommand";

export default class FileCommands {
    execute(name, ...args) {
        let cmd;

        switch (name) {
            case 'new project':
                cmd = new NewProjectCommand();
                break;
            case 'import file':
                cmd = new ImportFileCommand(args[0]);
                break;
            case 'import dir':
                cmd = new ImportDirCommand(args[0]);
                break;
            case 'import project':
                cmd = new ImportProjectCommand(args[0]);
                break;                
            case 'export glb':
                cmd = new ExportGlbCommand('scene.glb'); 
                break;
            case 'export project':
                cmd = new ExportProjectCommand(args[0]);
                break;
            case 'publish':
                new PublishProjectCommand().execute();
                new ExportGlbCommand('publish.glb').execute();
                return;
        }

        if (cmd)
            CommandManager.execute(cmd);
    }
}