import Command from "../command";

export default class SetObjectPropertyCommand extends Command {
    constructor(object, propName, value) {
        super();

        this.object = object;
        this.propName = propName;
        this.value = value;

        this.prevValue = object[propName];
    }

    get name() {
        return 'Set Property';
    }

    setValue(value) {
        this.setObjectValue(this.object, this.propName, value);
    }

    execute() {
        this.setValue(this.value);
    }

    undo() {
        this.setValue(this.prevValue);
    }

    redo() {
        this.execute();
    }
}