import EventAggregator from 'ezyvuecore/core/events/eventAggregator';
import { MathUtils, Mesh } from 'ezyvuecore/three/build/three';
import { ObjectDeseletedEvent, ObjectPositionChangedEvent, ObjectRotationChangedEvent, ObjectScaleChangedEvent, ObjectSelectedEvent } from '../editorEvents';
import SetPositionCommand from '../commands/setPositionCommand';
import { CommandManager } from '../commandManager';
import SetRotationCommand from '../commands/setRotationCommand';
import SetScaleCommand from '../commands/setScaleCommand';
import MaterialViewModel from './materialViewModel';
import { getObjectType } from '../utils';
import CameraSettingsViewModel from './cameraSettingsViewModel';
import AnnotaionViewModel from './annotationViewModel';
import AnimationsViewModel from './animationsViewModel';
import LightSettingsViewModel from './lightSettingsViewModel';

export default class Object3dPropertiesViewModel {
    constructor() {
        this.updateViewState = this.updateViewState.bind(this);
        this.onRotationChanged = this.onRotationChanged.bind(this);
        this.onRotationBlur = this.onRotationBlur.bind(this);
        this.onPositionChanged = this.onPositionChanged.bind(this);
        this.onPositionBlur = this.onPositionBlur.bind(this);

        this.visible = false;
        
        this.objectType = 'Node';

        this.materialViewModel = new MaterialViewModel();
        this.cameraViewModel = new CameraSettingsViewModel();
        this.annotationViewModel = new AnnotaionViewModel();
        this.animationsViewModel = new AnimationsViewModel();
        this.lightViewModel = new LightSettingsViewModel();

        EventAggregator.instance.subscribe(ObjectSelectedEvent, event => {
            this.object = event.object;

            this.objectType = getObjectType(this.object);

            if(this.object.isMesh) {
                this.materialViewModel.setMaterial(this.object.material);
            }
            else if(this.object.isCamera) {
                this.cameraViewModel.setCamera(this.object);
            }
            else if(this.object.isPoints) {
                this.annotationViewModel.setAnnotation(this.object);
            }
            else if(this.object.isLight) {
                this.lightViewModel.setLight(this.object);
            }

            this.animationsViewModel.setObject(this.object);

            this.visible = true;

            this.updateViewState();
        });

        EventAggregator.instance.subscribe(ObjectDeseletedEvent, () => {
            this.visible = false;

            this.updateViewState();

            this.object = null;
        });

        EventAggregator.instance.subscribe(ObjectPositionChangedEvent, event => {
            this.object = event.object;

            this.updateViewState();
        });

        EventAggregator.instance.subscribe(ObjectRotationChangedEvent, event => {
            this.object = event.object;

            this.updateViewState();
        });

        EventAggregator.instance.subscribe(ObjectScaleChangedEvent, event => {
            this.object = event.object;

            this.updateViewState();
        });
    }

    getType() {
        if(this.object instanceof Mesh)
            return 'Mesh';
        else
            return 'Object';
    }

    updateViewState(view = null) {
        if (view)
            this.view = view;

        if (this.view) {
            this.view.setState({visible: this.visible});

            this.object && this.view.setState({
                xPos: this.object.position.x.toFixed(4),
                yPos: this.object.position.y.toFixed(4),
                zPos: this.object.position.z.toFixed(4),
                xRot: MathUtils.radToDeg(this.object.rotation.x).toFixed(1),
                yRot: MathUtils.radToDeg(this.object.rotation.y).toFixed(1),
                zRot: MathUtils.radToDeg(this.object.rotation.z).toFixed(1),
                xScale: this.object.scale.x.toFixed(4),
                yScale: this.object.scale.y.toFixed(4),
                zScale: this.object.scale.z.toFixed(4),
                type: this.objectType,
                name: this.object.name,
            });
        }

        this.materialViewModel && this.materialViewModel.updateViewState();
    }

    onRotationChanged(e, axis) {
        this.view.setState({ [axis + 'Rot']: e.target.value });
    }

    onRotationBlur(e, axis) {
        if (e.key === 'Enter' && this.object) {
            const newRot = this.object.rotation.clone();
            newRot[axis] = MathUtils.degToRad(Number(e.target.value));

            const cmd = new SetRotationCommand(this.object, newRot, this.object.rotation);
            CommandManager.execute(cmd);
        }
    }

    onPositionChanged(e, axis) {
        this.view.setState({ [axis + 'Pos']: e.target.value });
    }

    onPositionBlur(e, axis) {
        if (e.key === 'Enter' && this.object) {
            const newPos = this.object.position.clone();
            newPos[axis] = Number(e.target.value);

            const cmd = new SetPositionCommand(this.object, newPos, this.object.position);
            CommandManager.execute(cmd);
        }
    }

    onScaleChanged(e, axis) {
        this.view.setState({ [axis + 'Scale']: e.target.value });
    }

    onScaleBlur(e, axis) {
        if (e.key === 'Enter' && this.object) {
            const newScale = this.object.scale.clone();
            newScale[axis] = Number(e.target.value);

            const cmd = new SetScaleCommand(this.object, newScale, this.object.scale);
            CommandManager.execute(cmd);
        }
    }

    onNameChanged(e) {
        if(this.object) {
            this.object.name = e.target.value;

            this.view.setState({name: e.target.value});
        }
    }
}