import Command from "../command";
import { Editor } from "../editor";

export default class SetObjectTransformModeCommand extends Command {
    constructor(mode, ) {
        super();

        this.originalMode = Editor.getObjectTransformMode();
        this.mode = mode;
    }

    get name() {
        return 'Select Transform Mode';
    }

    execute() {
        Editor.setObjectTransformMode(this.mode);
    }

    get undoable() { return false; }
}