import EditorFeatureManager from "../editor/editorFeatureManager";
import { ConfigModel } from "ezyvuecore/models/configModel";
import FileCommands from "./menus/fileCommands";
import EditCommands from "./menus/editCommands";
import EventAggregator from "ezyvuecore/core/events/eventAggregator";
import { ErrorEvent } from "common/applicationEvents";
import AddCommands from "./menus/addCommands";
import ObjectPropertiesTab from "./tabs/objectPropertiesTab";
import SceneTab from "./tabs/sceneTab";
import LightingTab from "./tabs/lightingTab";
import CameraControllerTab from "./tabs/cameraControllerTab";
import { ControllerTypeChangedEvent } from "./editorEvents";
import Project from "./project";
import EditorSettings from "./editorSettings";
import { Object3D } from "ezyvuecore/three/build/three";
import { Editor } from "./editor";
import AnnotationsTab from "./tabs/annotationsTab";
import ViewerTab from "./tabs/viewerTab";
import ViewCommands from "./menus/veiwCommands";
import EffectsTab from "./tabs/effectsTab";
import EditorSettingsTab from "./tabs/editorSettingsTab";

export default class EditorViewModel {
    constructor() {
        this.viewState = {
            errorMsg: '',
            message: '',
            tabs: [],
            showVirtualJoystick: false,
            responseDialogOpen: false
        };
                
        this.updateViewState = this.updateViewState.bind(this);
        this.onJoystickMove = this.onJoystickMove.bind(this);
        this.onJoystickRelease = this.onJoystickRelease.bind(this);
        this.onJoystickTouch = this.onJoystickTouch.bind(this);
        this.onResponseDialogOk = this.onResponseDialogOk.bind(this);
        this.onResponseDialogCancel = this.onResponseDialogCancel.bind(this);
        this.getUserResponse = this.getUserResponse.bind(this);
        this.onErrorAlertClose = this.onErrorAlertClose.bind(this);
        this.onMessageAlertClose = this.onMessageAlertClose.bind(this);

        EventAggregator.instance.subscribe(ErrorEvent, err => {
            this.viewState.errorMsg = err.message;

            this.updateViewState();
        });

        EventAggregator.instance.subscribe(ControllerTypeChangedEvent, event => {
            this.viewState.showVirtualJoystick = (event.type ==='fp');

            this.updateViewState();
        });

        this.loadedData = {};

        this.loadedData.projectConfig = new ConfigModel();
    }

    get featureManager() { return this._featureManager; }

    initialize(canvasElem, containerElem) {
        Editor.initialize(canvasElem, containerElem);

        this.fileCommands = new FileCommands();
        this.editCommands = new EditCommands();
        this.addCommands = new AddCommands();
        this.viewCommands = new ViewCommands();

        this.viewState.fileCommands = this.fileCommands;
        this.viewState.editCommands = this.editCommands;
        this.viewState.addCommands = this.addCommands;
        this.viewState.viewCommands = this.viewCommands;

        this.createFeatures();

        this.updateViewState();
    }

    updateViewState(view = null) {
        if (view)
            this.view = view;

        if (this.view) {
            this.view.setState({...this.viewState});
        }
    }

    onJoystickMove(x, y) {
        Editor.firstPersonCameraController && Editor.firstPersonCameraController.onVirtualJoyStickMove(x, y);
    }

    onJoystickTouch() {
        Editor.firstPersonCameraController && Editor.firstPersonCameraController.onVirtualJoyStickTouch();
    }

    onJoystickRelease() {
        Editor.firstPersonCameraController && Editor.firstPersonCameraController.onVirtualJoyStickRelease();
    }

    createFeatures() {
        this._featureManager = new EditorFeatureManager();

        this.viewState.tabs.push(new ObjectPropertiesTab());
        this.viewState.tabs.push(new SceneTab());
        this.viewState.tabs.push(new LightingTab());
        this.viewState.tabs.push(new CameraControllerTab());
        this.viewState.tabs.push(new AnnotationsTab());
        this.viewState.tabs.push(new EffectsTab())
        this.viewState.tabs.push(new ViewerTab());
        this.viewState.tabs.push(new EditorSettingsTab());

        this.updateViewState();
    }

    onResponseDialogOk() {
        this.responseCallback();

        this.viewState.responseDialogOpen = false;

        this.updateViewState();
    }

    onResponseDialogCancel() {
        this.viewState.responseDialogOpen = false;

        this.updateViewState();
    }

    onErrorAlertClose() {
        this.viewState.errorMsg = '';

        this.updateViewState();
    }

    onMessageAlertClose() {
        this.viewState.message = '';

        this.updateViewState();
    }

    getUserResponse(message, callback) {
        this.viewState.responseDialogOpen = true;
        this.viewState.responseDialogText = message;
        this.responseCallback = () => callback();

        this.updateViewState();
    }
}