import React, { Fragment } from "react"
import { PropTypes } from 'prop-types';
import TextField from '@material-ui/core/TextField';

export default class AnnotationView extends React.Component {
    constructor() {
        super();

        this.state = {};
    }

    render() {
        if(!this.props.viewModel || Object.keys(this.state).length === 0)
            return;

        return (
            <Fragment>
                <div>
                    Button Text&nbsp;
                    <TextField
                        value={this.state.buttonText}
                        style={{ width: '100px', marginRight: '2px' }}
                        onChange={e => this.props.viewModel.onPropertyChanged('buttonText', e.target.value)} />
                </div>
                <div className='editor-pane'>
                    Label Text
                    <textarea className='annotation-box-textinput' multiline rows="3" value={this.state.labelText}
                        onChange={e => this.props.viewModel.onPropertyChanged('labelText', e.target.value)} />
                </div>
            </Fragment>
        );
    }
}

AnnotationView.propTypes = {
    viewModel: PropTypes.object
}