import EventAggregator from "ezyvuecore/core/events/eventAggregator";
import { ControllerTypeChangedEvent, UndoRedoStatusChanged } from "../editorEvents";
import SetCameraControllerCommand from "../commands/setCameraControllerCommand";
import { CommandManager } from "../commandManager";
import { Editor } from "../editor";
import EditorSettings from "../editorSettings";
import { cloneDeep, set } from "lodash";

export default class CameraControllerSettingsViewModel {
    constructor() {
        this.updateViewState = this.updateViewState.bind(this);
        this.onPropertyChanged = this.onPropertyChanged.bind(this);

        EventAggregator.instance.subscribe(UndoRedoStatusChanged, () => this.updateViewState());

        this.updateViewState();
    }

    onPropertyChanged(e) {
        if(e.target.name === 'controllerType')
            EventAggregator.instance.notifyAll(new ControllerTypeChangedEvent(e.target.value));

        const state = cloneDeep(Editor.project.settings.state.cameraController);

        set(state, e.target.name, e.target.value);

        const cmd = new SetCameraControllerCommand(state, Editor.project.settings.state.cameraController);

        CommandManager.execute(cmd);

        this.updateViewState();
    }

    updateViewState(view = null) {
        if (view)
            this.view = view;

        if (this.view) {
            this.view.setState({
                targetName: Editor.world.cameraController?.object?.name ?? '',
                settings: Editor.project.settings.state.cameraController
            });
        }
    }
}