import GltfLoader from "ezyvuecore/engine/three/gltfLoader";
import { loadHdrTextureFromBlob } from "./textureFile";
import { AnimationClip, EquirectangularReflectionMapping, Object3D } from "ezyvuecore/three/build/three";
import { readFileAsText } from "./file";
import { Editor } from "./editor";
import { loadZipFile } from "./loadFromZip";
import EditorSettings from "./editorSettings";

export default class ProjectImporter {
    deserializeAnimations(scene) {
        scene.traverse(object => {
            if(object.userData.animations) {
                object.animations = [];

                object.userData.animations.forEach(json => {
                    object.animations.push(AnimationClip.parse(json));
                });
            }
        });
    }


    getSceneRoot(object) {
        if (!object.name.startsWith('AuxScene')) {
            return object;
        }

        if (object.children.length == 1) {
            return this.getSceneRoot(object.children[0]);
        }

        return object;
    }

    load(file, onLoad, onError) {
        try {
            let settings;
            let scene;
            let envMap;

            loadZipFile(file, async files => {
                for (const key in files) {
                    if (Object.hasOwnProperty.call(files, key)) {
                        const file = files[key];

                        if (key === 'scene.glb') {
                            const loader = new GltfLoader(Editor.world.renderer.renderer);
                            const gltf = await loader.parse(file.buffer, onError);

                            scene = gltf.scene;

                            // skip AuxScene added by exporter
                            while(scene.name.startsWith('AuxScene') && scene.children.length === 1) 
                                scene = scene.children[0];
                       
                            scene.parent && scene.removeFromParent();
                                
                            this.deserializeAnimations(scene);
                        }
                        else if (key === 'envmap.hdr') {
                            const blob = new Blob([file.buffer]);
                            envMap = await loadHdrTextureFromBlob(blob);

                            envMap.dataUrl = URL.createObjectURL(blob);

                            envMap.mapping = EquirectangularReflectionMapping;
                        }
                        else if (key === 'settings.json') {
                            const blob = new Blob([file.buffer]);
                            const json = await readFileAsText(blob);

                            settings = new EditorSettings();
                            settings.state = JSON.parse(json);
                        }
                    }
                }

                if (!scene)
                    scene = new Object3D();

                if (!settings)
                    settings = new EditorSettings();

                onLoad({
                    scene: scene,
                    settings: settings,
                    envMap: envMap
                });
            });

        }
        catch (e) {
            onError && onError('An error occured loading the project file');
        }
    }
}