import Command from "../command";
import { Editor } from "../editor";

export default class SetPositionCommand extends Command {
    constructor(object, pos, initialPos) {
        super();

        this.object = object;
        this.pos = pos.clone();
        this.initialPos = initialPos.clone();
    }

    get name() {
        return 'Set Position'
    }

    execute() {
        Editor.setObjectPosition(this.object, this.pos);
    }

    undo() {
        Editor.setObjectPosition(this.object, this.initialPos);
    }

    redo() {
        this.execute();
    }
}