import Command from "../command";
import { Editor } from "../editor";

export default class AddBakedShadowCommand extends Command {
    constructor(object) {
        super();

        this.object = object;
    }

    get name() {
        return 'Add Baked Shadow';
    }

    execute() {
        Editor.renderBakedShadow(this.object);
    }

    undo() {
        Editor.removeBakedShadowFromObject(this.object);
    }

    redo() {
        this.execute();
    }
}