import { cloneDeep, merge } from "lodash";
import Command from "../command";
import { Editor } from "../editor";

export default class SetAnnotationCommand extends Command {
    constructor(object, name, value) {
        super();

        this.object = object;
        
        this.prevUserData = cloneDeep(object.userData);
        this.userData = cloneDeep(object.userData);
        this.userData[name] = value;
    }

    get name() {
        return 'Set Annotation Property'
    }

    execute() {
        merge(this.object.userData, this.userData);

        Editor.updateAnnotations();
    }

    undo() {
        merge(this.object.userData, this.prevUserData);

        Editor.updateAnnotations();
    }

    redo() {
        this.execute();
    }
}