import { CommandManager } from "../commandManager";
import SetLightingCommand from "../commands/setLightingCommand";
import AddBakedShadowCommand from "../commands/addBakedShadowCommand";
import RemoveBakedShadowCommand from "../commands/removeBakedShadowCommand";
import EventAggregator from "ezyvuecore/core/events/eventAggregator";
import { ObjectDeseletedEvent, ObjectSelectedEvent } from "../editorEvents";
import SetBakedShadowCommand from "../commands/setBakedShadowCommand";
import { Editor } from "../editor";
import { cloneDeep } from "lodash";

export default class LightingSettingsViewModel {
    constructor() {
        this.savedProps = {};
        this.bakedShadowProps = {};

        this.updateViewState = this.updateViewState.bind(this);
        this.setProperty = this.setProperty.bind(this);
        this.onSliderChange = this.onSliderChange.bind(this);
        this.onSliderEnd = this.onSliderEnd.bind(this);
        this.onAddBakedShadowClicked = this.onAddBakedShadowClicked.bind(this);
        this.onRemoveBakedShadowClicked = this.onRemoveBakedShadowClicked.bind(this);
        this.onBakedShadowPropertyChanged = this.onBakedShadowPropertyChanged.bind(this);

        EventAggregator.instance.subscribe(ObjectSelectedEvent, e => {
            this.selectedObject = e.object;

            this.bakedShadowProps = Editor.getBakedShadowProperties(this.selectedObject);

            this.updateViewState();
        })

        EventAggregator.instance.subscribe(ObjectDeseletedEvent, () => {
            this.selectedObject = null;

            this.updateViewState();
        });
    }

    setProperty(prop, value) {
        const state = cloneDeep(Editor.project.settings.state.lighting);
        state[prop] = value;

        const cmd = new SetLightingCommand(state, Editor.project.settings.state.lighting);

        CommandManager.execute(cmd);

        this.updateViewState();
    }

    onSliderChange(prop, value) {
        if(!this.savedSettings)
            this.savedSettings = cloneDeep(Editor.project.settings.state.lighting);

        this.setProperty(prop, value, false);
    }

    onAddBakedShadowClicked() {
        if( Editor.selectedObject) {
            const cmd = new AddBakedShadowCommand(Editor.selectedObject);

            CommandManager.execute(cmd);
            
            this.showAddButton = false;
            this.updateViewState();
        }
    }

    onRemoveBakedShadowClicked() {
        if( Editor.selectedObject) {
            const cmd = new RemoveBakedShadowCommand(Editor.selectedObject);

            CommandManager.execute(cmd);

            this.showAddButton = true;
            this.updateViewState();
        }
    }

    onRefreshBakedShadowClicked() {
    }

    onSliderEnd() {
        const cmd = new SetLightingCommand(Editor.project.settings.state.lighting, this.savedSettings);

        CommandManager.execute(cmd);

        this.updateViewState();
        this.savedSettings = null;
    }

    onBakedShadowPropertyChanged(prop, value) {
        if(this.selectedObject) {
            this.bakedShadowProps[prop] = value;

            const cmd = new SetBakedShadowCommand(this.selectedObject, this.bakedShadowProps);

            CommandManager.execute(cmd);

            this.updateViewState();
        }
    }

    updateViewState(view = null) {
        if (view)
            this.view = view;

        if (this.view) {
            this.view.setState({
                showControls: this.selectedObject != null,
                showAddButton: this.selectedObject?.bakedShadow == null,
                bakedShadowBlur: this.bakedShadowProps.bakedShadowBlur,
                bakedShadowLightness: this.bakedShadowProps.bakedShadowLightness,
                bakedShadowFalloff: this.bakedShadowProps.bakedShadowFalloff,          
                settings: Editor.project.settings.state.lighting
            });
        }
    }
}