import React from "react";
import PropTypes from 'prop-types';
import Slider from '@material-ui/core/Slider';
import IconButton from '@material-ui/core/IconButton';
import InputLabel from '@material-ui/core/InputLabel';
import RefreshIcon from '@material-ui/icons/Refresh';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import RemoveCircleIcon from '@material-ui/icons/RemoveCircle';
import TextField from '@material-ui/core/TextField';

export default class LightingSettingsView extends React.Component {
    constructor() {
        super();

        this.state = {};
    }

    render() {
        if (!this.props.viewModel || Object.keys(this.state).length === 0)
            return;

        return (
            <div className='editor-pane'>
                <div className='editor-sidebar-heading'>
                    Shadows
                </div>
                <div>
                    <label className="checkboxLabel">Enabled
                        <input
                            type="checkbox"
                            checked={this.state.settings.shadowsEnabled}
                            onChange={e => this.props.viewModel.setProperty('shadowsEnabled', e.target.checked)} />
                        <span className="checkboxCheckmark"></span>
                    </label>
                </div>
                <div>
                    <TextField
                        label="Shadow Bias" type='number' InputLabelProps={{ shrink: true }} value={this.state.settings.shadowBias}
                        onChange={e => this.props.viewModel.setProperty('shadowBias', e.target.value)} />
                </div>
                {
                    this.state.settings.shadowsEnabled &&
                    <div>
                        <div>
                            <label className="checkboxLabel">Shadow Plane
                                <input
                                    type="checkbox"
                                    checked={this.state.settings.shadowPlaneVisible}
                                    onChange={e => this.props.viewModel.setProperty('shadowPlaneVisible', e.target.checked)} />
                                <span className="checkboxCheckmark"></span>
                            </label>
                        </div>
                        {
                            this.state.settings.shadowPlaneVisible &&
                            <div className='flexrow'>
                                <div className='flexcol'>
                                    Lightness&nbsp;
                                </div>
                                <div className='flexcol'>
                                    <Slider
                                        style={{ width: '200px' }}
                                        value={this.state.settings.shadowPlaneLightness * 100}
                                        onChange={(e, value) => this.props.viewModel.onSliderChange('shadowPlaneLightness', value / 100)}
                                        onChangeCommitted={(e, value) => this.props.viewModel.onSliderEnd('shadowPlaneLightness', value * 100)} />
                                </div>
                            </div>
                        }
                        <div className='ctl-label'>Shadow Light</div>
                        <div className='flexrow'>
                            <div className='flexcol'>
                                Rotation
                            </div>
                            <div className='flexcol'>
                                <Slider
                                    style={{ width: '200px', marginRight: '8px' }}
                                    value={(this.state.settings.shadowLightRotation / (2 * Math.PI)) * 100}
                                    onChange={(e, value) => this.props.viewModel.onSliderChange('shadowLightRotation', (value / 100) * 2 * Math.PI)}
                                    onChangeCommitted={(e, value) => this.props.viewModel.onSliderEnd('shadowLightRotation', (value / 100) * 2 * Math.PI)}
                                />
                            </div>
                        </div>
                        <div className='flexrow'>
                            <div className='flexcol'>
                                Elevation
                            </div>
                            <div className='flexcol'>
                                <Slider
                                    style={{ width: '200px', marginRight: '8px' }}
                                    value={(this.state.settings.shadowLightElevation / (2 * Math.PI)) * 100}
                                    onChange={(e, value) => this.props.viewModel.onSliderChange('shadowLightElevation', (value / 100) * 2 * Math.PI)}
                                    onChangeCommitted={(e, value) => this.props.viewModel.onSliderEnd('shadowLightElevation', (value / 100) * 2 * Math.PI)} />
                            </div>
                        </div>
                    </div>
                }
                <div className='editor-sidebar-heading'>
                    Baked Shadow
                </div>
                {
                    this.state.showControls ?
                        <div>
                            <div className='flexrow'>
                                <IconButton onClick={this.props.viewModel.onRefereshBakedShadowClicked}>
                                    <RefreshIcon />
                                </IconButton>
                                <IconButton disabled={!this.state.showAddButton} onClick={this.props.viewModel.onAddBakedShadowClicked}>
                                    <AddCircleIcon />
                                </IconButton>
                                <IconButton disabled={this.state.showAddButton} onClick={this.props.viewModel.onRemoveBakedShadowClicked}>
                                    <RemoveCircleIcon />
                                </IconButton>
                            </div>
                            <div className='flexrow'>
                                <div className='flexcol'>
                                    Softness
                                </div>
                                <div className='flexcol'>
                                    <Slider
                                        style={{ width: '200px', marginRight: '8px' }}
                                        value={this.state.bakedShadowBlur * 100}
                                        onChange={(e, value) => this.props.viewModel.onBakedShadowPropertyChanged('bakedShadowBlur', value / 100)} />
                                </div>
                            </div>
                            <div className='flexrow'>
                                <div className='flexcol'>
                                    Lightness
                                </div>
                                <div className='flexcol'>
                                    <Slider
                                        style={{ width: '200px', marginRight: '8px' }}
                                        value={this.state.bakedShadowLightness * 100}
                                        onChange={(e, value) => this.props.viewModel.onBakedShadowPropertyChanged('bakedShadowLightness', value / 100)} />
                                </div>
                            </div>
                            <div className='flexrow'>
                                <div className='flexcol'>
                                    Depth
                                </div>
                                <div className='flexcol'>
                                    <Slider
                                        style={{ width: '200px', marginRight: '8px' }}
                                        value={this.state.bakedShadowFalloff * 100}
                                        onChange={(e, value) => this.props.viewModel.onBakedShadowPropertyChanged('bakedShadowFalloff', value / 100)} />
                                </div>
                            </div>
                        </div>
                        :
                        <div>
                            Select an object to edit
                        </div>
                }
                <div className='editor-sidebar-heading'>
                    Ambient Light
                </div>
                <InputLabel>Brightness</InputLabel>
                <Slider style={{ width: '200px' }}
                    value={this.state.settings.ambientLightIntensity * 100}
                    onChange={(e, value) => this.props.viewModel.setProperty('ambientLightIntensity', value / 100)} />
                <InputLabel>Color</InputLabel>
                <input type='color'
                    value={this.state.settings.ambientLightColor}
                    onChange={(e) => this.props.viewModel.setProperty('ambientLightColor', e.target.value)} />
            </div>
        );
    }
}

LightingSettingsView.propTypes = {
    viewModel: PropTypes.object,
};