import { cloneDeep } from "lodash";
import Command from "../command";
import { Editor } from "../editor";

export default class SetLightingCommand extends Command {
    constructor(settings, prevSettings) {
        super();

        this.settings = cloneDeep(settings);
        this.prevSettings = cloneDeep(prevSettings);
    }

    get name() {
        return 'Set Lighting Property';
    }

    execute() {
        Editor.project.settings.state = {lighting: this.settings};

        Editor.setLighting();
    }

    undo() {
        Editor.project.settings.state = {lighting: this.prevSettings};

        Editor.setLighting();
    }

    redo() {
        this.execute();   
    }
}