import { AlertEvent } from "common/applicationEvents";
import EventAggregator from "ezyvuecore/core/events/eventAggregator";
import { getCameraFloorIntersection, renderToDataUrl } from '../utils'
import { Editor } from "../editor";
import { ProjectSettingsChangedEvent, SceneChangedEvent } from "../editorEvents";
import { cloneDeep, set } from "lodash";

export default class ViewerSettingsViewModel {
    constructor() {
        this.viewState = {
            imageSrc: null,
        };

        this.updateViewState = this.updateViewState.bind(this);
        this.onCaptureScreen = this.onCaptureScreen.bind(this);
        this.onSetVRPosition = this.onSetVRPosition.bind(this);
        this.onClearVRPosition = this.onClearVRPosition.bind(this);
        this.onPropertyChanged = this.onPropertyChanged.bind(this);

        EventAggregator.instance.subscribe(ProjectSettingsChangedEvent, () => this.updateViewState());

        EventAggregator.instance.subscribe(SceneChangedEvent, () => {
            if(Editor.project.loadingImage)
                this.viewState.loadingImage = Editor.project.loadingImage;

            this.updateViewState()
        });
    }
    
    onSetVRPosition() {
        const floorPoint = getCameraFloorIntersection(this.world.camera, this.scene);

        this.model.initialVrPosition = [
            this.world.camera.position.x,
            floorPoint ? floorPoint.y : 0,
            this.world.camera.position.z,
        ];

        this.model.initialVrRotation = [
            this.world.camera.rotation.x,
            this.world.camera.rotation.y,
            this.world.camera.rotation.z
        ];

        this.updateViewState();

        EventAggregator.instance.notifyAll(new AlertEvent('Initial VR position set. Save the scene to save settings.'));
    }

    onClearVRPosition() {
        this.model.initialVrPosition = null;
        this.model.initialVrRotation = null;

        this.updateViewState();

        EventAggregator.instance.notifyAll(new AlertEvent('Initial VR position cleared. Save the scene.'));
    }

    onPropertyChanged(name, value) {
        const state = {};

        set(state, name, value);

        Editor.project.settings.state = {...state};

        this.updateViewState();
    }
    
    async onCaptureScreen() {
        const dataUrl = renderToDataUrl(Editor.world);

        this.viewState.loadingImage = dataUrl;

        Editor.project.loadingImage = dataUrl;

        this.updateViewState();
    }
    
    updateViewState(view = null) {
        if(view)
            this.view = view;

        if(this.view) {            
            this.view.setState({
                settings: Editor.project.settings.state,
                viewState: {...this.viewState}
            });
        }
    }
}