import Event from "ezyvuecore/core/events/event";

export class UndoRedoStatusChanged extends Event {}

export class SceneModifiedEvent extends Event {}
export class SceneChangedEvent extends Event {}

export class ViewportClickEvent extends Event {
    constructor(domEvent) {
        super();

        this.domEvent = domEvent;
    }
}

export class RayObjectIntersectionEvent extends Event {
    constructor(intersect) {
        super();

        this.intersect = intersect;
    }
}

export class ObjectSelectedEvent extends Event {
    constructor(object, intersect) {
        super();

        this.object = object;
        this.intersect = intersect;
    }
}

export class ObjectDeseletedEvent extends Event {
    constructor(object) {
        super();

        this.object = object;
    }
}

export class ObjectPositionChangedEvent extends Event {
    constructor(object) {
        super();

        this.object = object;
    }
}

export class ObjectRotationChangedEvent extends Event {
    constructor(object) {
        super();

        this.object = object;
    }
}

export class ObjectScaleChangedEvent extends Event {
    constructor(object) {
        super();

        this.object = object;
    }
}

export class ProjectChangedEvent extends Event {
}

export class ProjectSettingsChangedEvent extends Event {
    constructor(settings) {
        super();

        this.settings = settings;
    }
}

export class ControllerTypeChangedEvent extends Event {
    constructor(type) {
        super();

        this.type = type;
    }
}

