import { SpotLight } from "ezyvuecore/three/build/three";
import { CommandManager } from "../commandManager";
import EventAggregator from "ezyvuecore/core/events/eventAggregator";
import { UndoRedoStatusChanged } from "../editorEvents";
import SetLightPropertyCommand from "../commands/setLightPropertyCommand";

export default class LightSettingsViewModel {
    constructor() {
        this.settings = {};

        this.updateViewState = this.updateViewState.bind(this);
        this.onPropChanged = this.onPropChanged.bind(this);

        EventAggregator.instance.subscribe(UndoRedoStatusChanged, () => {
            this.light && this.setLight(this.light);
        })
    }

    setLight(light) {
        this.light = light;

        this.settings = {
            color: '#' + light.color.getHexString(),
            intensity: light.intensity,
            power: light.power,
            angle: (light instanceof SpotLight) ? light.angle : 0,
            isSpotlight: light instanceof SpotLight
        };

        this.updateViewState();
    }

    onPropChanged(e) {
        const cmd = new SetLightPropertyCommand(this.light, e.target.name, e.target.value);

        CommandManager.execute(cmd);

        this.settings[e.target.name] = e.target.value;

        this.updateViewState();
    }

    updateViewState(view = null) {
        if (view)
            this.view = view;

        if (this.view && this.light)
            this.view.setState({ ...this.settings });   
    }
}