import PropTypes from 'prop-types';
import React from "react";

export default class VirtualJoyStick extends React.Component {
    constructor(props) {
        super(props);

        this.stickRef = React.createRef();

        this.onPointerDown = this.onPointerDown.bind(this);
        this.onPointerMove = this.onPointerMove.bind(this);
        this.onPointerUp = this.onPointerUp.bind(this);
    }

    render() {
        return (
            <div style={{
                width: '128px',
                position: 'absolute',
                left: '10px',
                top: '10px',
                touchAction: 'none'
            }}
                onPointerMove={this.onPointerMove} >
                <img src={this.props.baseImageSrc}
                    style={{ '-webkit-user-drag': 'none', userSelect: 'none', width: '80px', height: '80px' }} />
                <div ref={this.stickRef}
                    style={{ position: 'absolute', left: '20px', top: '20px' }}
                    onPointerDown={this.onPointerDown} >
                    <img src={this.props.stickImageSrc}
                        style={{ '-webkit-user-drag': 'none', userSelect: 'none', width: '40px', height: '40px' }} />
                </div>
            </div>
        );
    }

    componentDidMount() {
        this.dragStart = null;
        this.touchId = null;
        this.active = false;

        document.addEventListener('pointerup', this.onPointerUp)
        document.addEventListener('pointerleave', this.onPointerUp)
    }

    onPointerDown(event) {
        event.stopPropagation();

        this.active = true;

        this.stickRef.current.style.transition = '0s';

        event.preventDefault();
        event.stopPropagation();

        if (event.changedTouches)
            this.dragStart = { x: event.changedTouches[0].clientX, y: event.changedTouches[0].clientY };
        else
            this.dragStart = { x: event.clientX, y: event.clientY };

        if (event.changedTouches)
            this.touchId = event.changedTouches[0].identifier;

        this.onTouch && this.onTouch();
    }

    onPointerMove(event) {
        event.stopPropagation();

        if (!this.active)
            return;

        let touchmoveId = null;
        if (event.changedTouches) {
            for (let i = 0; i < event.changedTouches.length; i++) {
                if (this.touchId == event.changedTouches[i].identifier) {
                    touchmoveId = i;
                    event.clientX = event.changedTouches[i].clientX;
                    event.clientY = event.changedTouches[i].clientY;
                }
            }

            if (touchmoveId == null)
                return;
        }

        const xDiff = event.clientX - this.dragStart.x;
        const yDiff = event.clientY - this.dragStart.y;
        const angle = Math.atan2(yDiff, xDiff);
        const distance = Math.min(this.props.maxDistance, Math.hypot(xDiff, yDiff));
        const xPosition = distance * Math.cos(angle);
        const yPosition = distance * Math.sin(angle);

        this.stickRef.current.style.transform = `translate3d(${xPosition}px, ${yPosition}px, 0px)`;

        const distance2 = (distance < this.props.deadzone) ? 0 : this.props.maxDistance / (this.props.maxDistance - this.props.deadzone) * (distance - this.props.deadzone);
        const xPosition2 = distance2 * Math.cos(angle);
        const yPosition2 = distance2 * Math.sin(angle);

        this.props.onMove && this.props.onMove(xPosition2, yPosition2);
    }

    onPointerUp(event) {
        event.stopPropagation();

        if (!this.active)
            return;

        if (event.changedTouches && this.touchId != event.changedTouches[0].identifier)
            return;

        this.stickRef.current.style.transition = '.2s';
        this.stickRef.current.style.transform = `translate3d(0px, 0px, 0px)`;

        this.touchId = null;
        this.active = false;

        this.props.onRelease && this.props.onRelease();
    }
}

VirtualJoyStick.propTypes = {
    onRelease: PropTypes.func,
    onMove: PropTypes.func,
    maxDistance: PropTypes.number,
    deadzone: PropTypes.number,
    baseImageSrc: PropTypes.string,
    stickImageSrc: PropTypes.string
}