import React from "react";
import TextureSelector from "../textureSelector";
import PropTypes from 'prop-types';
import Select from '@material-ui/core/Select';
import Slider from '@material-ui/core/Slider';
import TextField from '@material-ui/core/TextField';
import { Fragment } from "react";
import Divider from '@material-ui/core/Divider';

export default class EnvironmentSettingsView extends React.Component {
    constructor() {
        super();

        this.state = {};
    }

    render() {
        if (!this.props.viewModel || Object.keys(this.state).length === 0)
            return;

        return (
            <div className='editor-pane'>
                <div className='editor-sidebar-heading'>
                    Environment Map
                </div>
                <div className='flexrow'>
                    <TextureSelector texture={this.state.envMap}
                        onChange={this.props.viewModel.onEnvMapChanged}
                        onDelete={this.props.viewModel.onEnvMapDelete} />
                </div>
                <div className='editor-sidebar-heading'>
                    Background
                </div>
                <div style={{ display: 'flex' }}>
                    <Select
                        native
                        value={this.state.settings.backgroundType}
                        onChange={e => this.props.viewModel.setProperty('backgroundType', e.target.value)}
                        style={{ minWidth: '200px', marginBottom: '10px' }} >
                        <option value='transparent'>Transparent</option>
                        <option value='gradient'>Gradient</option>
                        <option value='color'>Color</option>
                        {/*  <option value={2}>Skybox</option> */}
                    </Select>
                </div>
                {
                    this.state.settings.backgroundType === 'color' &&
                    <div className='flexrow-la'>
                        <div className='ctl-label'>Background Color&nbsp;</div>
                        <input type='color'
                            value={this.state.settings.backgroundColor}
                            onChange={e => this.props.viewModel.setProperty('backgroundColor', e.target.value)} />
                    </div>
                }
                {
                    this.state.settings.backgroundType === 'gradient' &&
                    <div>
                        <div className='ctl-label'>Background Gradient</div>
                        <input type='color'
                            value={this.state.settings.backgroundGradientColor1}
                            onChange={e => this.props.viewModel.setProperty('backgroundGradientColor1', e.target.value)} />
                        <input type='color'
                            value={this.state.settings.backgroundGradientColor2}
                            onChange={e => this.props.viewModel.setProperty('backgroundGradientColor2', e.target.value)} />
                    </div>
                }
                {
                    this.state.settings.backgroundType === 'transparent' &&
                    <div>
                    </div>
                }
                <div className='editor-sidebar-heading'>
                    Reflection Plane
                </div>
                <div>
                    <label className="checkboxLabel">Enable Reflection
                        <input
                            type="checkbox"
                            checked={this.state.settings.enableReflection}
                            onChange={e => this.props.viewModel.setProperty('enableReflection', e.target.checked)} />
                        <span className="checkboxCheckmark"></span>
                    </label>
                    {
                        this.state.settings.enableReflection &&
                        <div>
                            <Fragment>
                                <TextField
                                    label="Fade Depth"
                                    type='number'
                                    value={this.state.settings.reflectionFadeScale}
                                    style={{ minWidth: '100px' }}
                                    inputProps={{ min: 0 }}
                                    onChange={e => this.props.viewModel.setProperty('reflectionFadeScale', e.target.value)} />
                                <div className='flexrow-la'>
                                    <div className='ctl-label'>Color&nbsp;</div>
                                    <input type='color'
                                        value={this.state.settings.reflectionColor}
                                        onChange={e => this.props.viewModel.setProperty('reflectionColor', e.target.value)} />
                                </div>
                                <div className='ctl-label'>Opacity</div>
                                <Slider style={{ width: '200px' }}
                                    value={this.state.settings.reflectionOpacity * 100}
                                    onChange={(e, value) => this.props.viewModel.setProperty('reflectionOpacity', value / 100)} />
                                <label className="checkboxLabel">Blur Reflection
                                    <input
                                        type="checkbox"
                                        checked={this.state.settings.blurReflection}
                                        onChange={e => this.props.viewModel.setProperty('blurReflection', e.target.checked)} />
                                    <span className="checkboxCheckmark"></span>
                                </label>
                            </Fragment>
                        </div>
                    }
                </div>
                <div className='editor-sidebar-heading'>
                    Fog
                </div>
                <div>

                    <label className="checkboxLabel">Fog
                        <input
                            type="checkbox"
                            checked={this.state.settings.fog}
                            onChange={e => this.props.viewModel.setProperty('fog', e.target.checked)} />
                        <span className="checkboxCheckmark"></span>
                    </label>
                </div>
                {
                    this.state.settings.fog &&
                    <Fragment>
                        <div className='flexrow-la'>
                            <label className="radioContainer"
                                style={{ marginLeft: '5px' }}
                                onChange={() => this.props.viewModel.setProperty('fogType', 'exponential')}>Exponential
                                <input type="radio" checked={this.state.settings.fogType === 'exponential'} />
                                <span className="radioCheckmark"></span>
                            </label>
                            <label
                                className="radioContainer"
                                style={{ marginLeft: '5px' }}
                                onChange={() => this.props.viewModel.setProperty('fogType', 'linear')}>Linear
                                <input type="radio" checked={this.state.settings.fogType === 'linear'} />
                                <span className="radioCheckmark"></span>
                            </label>
                        </div>
                        {
                            this.state.settings.fogType === 'linear' &&
                            <div>
                                <TextField
                                    label="Fog Near"
                                    type='number'
                                    value={this.state.settings.fogNear}
                                    onChange={e => this.props.viewModel.setProperty('fogNear', e.target.value)} /><br />
                                <TextField
                                    label="Fog Far"
                                    type='number'
                                    value={this.state.settings.fogFar}
                                    onChange={e => this.props.viewModel.setProperty('fogFar', e.target.value)} /><br />
                            </div>
                        }
                        {
                            this.state.settings.fogType === 'exponential' &&
                            <TextField
                                label="Density"
                                type='number'
                                value={this.state.settings.fogDensity}
                                onChange={e => this.props.viewModel.setProperty('fogDensity', e.target.value)} />
                        }
                        <div className='flexrow-la'>
                            <div className='ctl-label'>Color</div>
                            <input type='color'
                                value={this.state.settings.fogColor}
                                onChange={(e) => this.props.viewModel.setProperty('fogColor', e.target.value)} />
                        </div>
                        <br />
                        <br />
                        <br />
                    </Fragment>
                }
                <Divider />
                <label className="checkboxLabel">Preview
                    <input type="checkbox" checked={this.state.viewState.preview}
                        onChange={e => this.props.viewModel.onViewStateChanged('preview', e.target.checked)} />
                    <span className="checkboxCheckmark"></span>
                </label>
            </div>
        )
    }
}

EnvironmentSettingsView.propTypes = {
    viewModel: PropTypes.object
};