import { CommandManager } from "../commandManager";
import SetEffectsCommand from "../commands/setEffectsCommands";
import { Editor } from "../editor";

export default class EffectsViewModel {
    constructor() {
        this.updateViewState = this.updateViewState.bind(this);

        this.viewState = {
            preview: false
        };
    }
    
    onPropertyChanged(name, value) {
        const cmd = new SetEffectsCommand(name, value);

        CommandManager.execute(cmd);

        this.updateViewState();
    }

    onViewStateChanged(name, value) {
        this.viewState[name] = value;

        Editor.effectsPreviewMode = this.viewState.preview;

        this.updateViewState();
    }
    
    updateViewState(view = null) {
        if(view)
            this.view = view;

        const effects = Editor.project.settings.state.effects;

        if(this.view)
            this.view.setState({
                settings: effects,
                viewState: this.viewState
            });
    }
}