import React, { Fragment } from "react";
import TreeView from '@material-ui/lab/TreeView';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import TreeItem from '@material-ui/lab/TreeItem'
import { getObjectType } from "../utils";
import PropTypes from 'prop-types';

export default class SceneGraphView extends React.Component {
    constructor() {
        super();

        this.state = {
            tree: null,
            expandedNodeIds: [],
            selectedNodeIds: []
        }
    }

    render() {
        if (!this.state.tree)
            return null;

        return (
            <Fragment>
                <div className='editor-sidebar-heading'>
                    Scene Graph
                </div>
                <TreeView
                    defaultCollapseIcon={<ExpandMoreIcon />} 
                    defaultExpandIcon={<ChevronRightIcon />}
                    defaultExpanded={this.state.expandedNodeIds}
                    defaultSelected={this.state.selectedNodeIds}
                    onNodeToggle={this.props.viewModel.onNodeToggle}
                    onNodeSelect={this.props.viewModel.onNodeSelected}>
                    {
                        this.renderTree(this.state.tree)
                    }
                </TreeView>
            </Fragment>
        )
    }

    renderTree(nodes) {
        return (
            <TreeItem 
                key={nodes.id} 
                nodeId={nodes.id} label={getObjectType(nodes.object)} 
                onClick={() => this.props.viewModel.onObjectClicked(nodes.object)} >
                {
                    Array.isArray(nodes.children)
                        ?
                        nodes.children.map((node) => this.renderTree(node))
                        :
                        null
                }
            </TreeItem>
        );
    }
}

SceneGraphView.propTypes = {
    viewModel: PropTypes.object
}