import Command from "../command";
import { Editor } from "../editor";
import { createBox, createCapsule, createCircle, createCylinder, createDodecahedron, createIcosahedron, createLathe, createPlane, createRing, createSphere, createTetrahedron, createTorus, createTube } from "../primitives";

export default class AddPrimitiveCommand extends Command {
    constructor(type) {
        super();

        this.type = type;
    }

    get name() {
        return 'Add ' + this.type;
    }

    execute() {
        switch (this.type) {
            case 'Sphere':
                this.object = createSphere();
                break;
            case 'Box':
                this.object = createBox();
                break;
            case 'Cylinder':
                this.object = createCylinder();
                break;
            case 'Torus':
                this.object = createTorus();
                break;
            case 'Capsule':
                this.object = createCapsule();
                break;
            case 'Tetrahedron':
                this.object = createTetrahedron();
                break;
            case 'Icosahedron':
                this.object = createIcosahedron();
                break;
            case 'Dodecahedron':
                this.object = createDodecahedron();
                break;
            case 'Lathe':
                this.object = createLathe();
                break;
            case 'Plane':
                this.object = createPlane();
                break;
            case 'Circle':
                this.object = createCircle();
                break;
            case 'Ring':
                this.object = createRing();
                break;
            case 'Tube':
                this.object = createTube();
                break;
        }

        if(this.object) {
            Editor.addObject(this.object);

            Editor.selectObject(this.object);
        }
    }

    undo() {
        Editor.removeObject(this.object);
    }

    redo() {
        Editor.addObject(this.object);
    }
}