import Command from "../command";
import { Editor } from "../editor";
import { createAnnotation } from "../sceneElements";

export default class AddAnnotationCommand extends Command {
    constructor(settings, position, buttonText, labelText, parent) {
        super();

        this.parent = parent;
        this.settings = settings;
        this.position = position;
        this.buttonText = buttonText;
        this.labelText = labelText;
    }

    get name() { return 'Add Annotation'; }

    execute() {
        this.annot = createAnnotation(this.settings.size, this.buttonText, this.labelText, this.settings.fillColor, this.settings.textColor,
            this.settings.borderColor, Editor.world.cameraController.getState());

        this.annot.position.copy(this.position);

        Editor.addAnnotation(this.annot, this.parent);

        return this.annot;
    }

    undo() {
        Editor.removeAnnotation(this.annot);
    }

    redo() {
        this.execute();
    }
}