import EditorTab from "members/features/editor/editorTab";
import Tooltip from '@material-ui/core/Tooltip';
import React from "react";
import { Fragment } from "react";
import SettingsIcon from '@material-ui/icons/Settings';
import EditorSettingsViewModel from "../viewModels/editorSettingsViewModel";
import EditorSettingsView from "../views/editorSettingsView";

export default class EditorSettingsTab extends EditorTab {
    constructor() {
        super();

        this.viewModel = new EditorSettingsViewModel();
    }

    renderButtonIcon() {
        return (
            <SettingsIcon />
        )
    }

    renderContent() {
        return (
            <Fragment>
                <div className='editor-sidebar-heading' style={{ fontSize: 'large' }}>
                    Preferences
                </div>
                <EditorSettingsView viewModel={this.viewModel} ref={this.viewModel.updateViewState} />
            </Fragment>
        );
    }
}