import React, { Fragment } from "react"
import Select from '@material-ui/core/Select';
import TextField from '@material-ui/core/TextField';
import { PropTypes } from 'prop-types';

export default class CameraControllerSettingsView extends React.Component {
    constructor() {
        super();

        this.state = {};
    }

    render() {
        if(!this.props.viewModel || Object.keys(this.state).length === 0)
            return;

        return (
            <Fragment>
                <div className='editor-pane'>
                    <div className='editor-sidebar-heading'>
                        Controller Type
                    </div>
                    <div className='flexrow-la'>
                        <Select
                            native
                            name='controllerType'
                            value={this.state.settings.controllerType}
                            style={{ width: '200px' }}
                            onChange={this.props.viewModel.onPropertyChanged} >
                            <option value={'orbit'}>Orbit</option>
                            <option value={'fp'}>First Person</option>
                        </Select>
                    </div>
                    <div className='editor-sidebar-heading'>
                        Camera Target
                    </div>
                    <div>
                        {
                            <TextField
                            value={this.state.targetName}
                            style={{ maxWidth: '200px' }}
                            disabled
                            inputProps={{ min: 0}} />
                        }
                    </div>
                 {/*    <div>
                       {

                        !this.state.selectedObject ?
                        <div>
                            To change select a camera
                        </div>
                        :
                        <Button
                            style={{ marginRight: '10px' }}
                            variant='contained'
                            color='primary'
                            onClick={this.props.viewModel.onSetTargetClicked}>
                            Set
                        </Button>
    }
                    </div> */}
                    <div className='editor-sidebar-heading'>
                        Properties
                    </div>
                    {
                        this.state.settings.controllerType === 'orbit' &&
                        <Fragment>
                            <TextField
                                label="Minimum Zoom Distance"
                                type='number'
                                name='orbitController.minZoom'
                                value={this.state.settings.orbitController.minZoom}
                                style={{ maxWidth: '200px' }}
                                inputProps={{ min: 0 }}
                                onBlur={this.props.viewModel.onPropertyBlur}
                                onKeyUp={this.props.viewModel.onPropertyBlur}
                                onChange={this.props.viewModel.onPropertyChanged} />
                            <TextField
                                label="Maximum Zoom Distance"
                                type='number'
                                name='orbitController.maxZoom'
                                value={this.state.settings.orbitController.maxZoom}
                                style={{ maxWidth: '200px' }}
                                inputProps={{ min: 0 }}
                                onBlur={this.props.viewModel.onPropertyBlur}
                                onKeyUp={this.props.viewModel.onPropertyBlur}
                                onChange={this.props.viewModel.onPropertyChanged} />
                            <TextField
                                label="Minimum Polar Angle"
                                type='number'
                                name='orbitController.minElevation'
                                value={this.state.settings.orbitController.minElevation}
                                style={{ maxWidth: '200px' }}
                                inputProps={{min: 0, max: 180}}
                                onBlur={this.props.viewModel.onPropertyBlur}
                                onKeyUp={this.props.viewModel.onPropertyBlur}
                                onChange={this.props.viewModel.onPropertyChanged} />
                            <TextField
                                label="Maximum Polar Angle"
                                type='number'
                                name='orbitController.maxElevation'
                                value={this.state.settings.orbitController.maxElevation}
                                style={{ maxWidth: '200px' }}
                                inputProps={{min: 0,max: 180}}
                                onBlur={this.props.viewModel.onPropertyBlur}
                                onKeyUp={this.props.viewModel.onPropertyBlur}
                                onChange={this.props.viewModel.onPropertyChanged} />
                        </Fragment>
                    }
                    {
                        this.state.settings.controllerType == 'fp' &&
                        <Fragment>
                            <TextField
                                label="Move Speed"
                                type='number'
                                name='firstPersonController.moveSpeed'
                                value={this.state.settings.firstPersonController.moveSpeed}
                                style={{ maxWidth: '200px' }}
                                inputProps={{ min: 0 }}
                                onBlur={this.props.viewModel.onPropertyBlur}
                                onKeyUp={this.props.viewModel.onPropertyBlur}
                                onChange={this.props.viewModel.onPropertyChanged} />
                        </Fragment>
                    }
                </div>
                <div className='editor-sidebar-heading'>
                    Controls
                </div>
                {
                    this.state.settings.controllerType === 'fp' &&
                    <div className='flexrow'>
                        <div className='flexcol'>
                            Look around<br />
                            Foward/back<br />
                            Left/right<br />
                            Up/down<br />
                        </div>
                        <div className='flexcol'>
                            Mouse<br />
                            W/S or virtual joystick<br />
                            A/D or virtual joystick<br />
                            R/F
                        </div>
                    </div>
                }
                {
                    this.state.settings.cameraControllerType == 'oribit' &&
                    <div className='flexrow'>
                        <div className='flexcol'>
                            Orbit around<br />
                            Zoom<br />
                            Pan<br />
                        </div>
                        <div className='flexcol'>
                            Mouse<br />
                            Mouse wheel<br />
                            Mouse + CTRL<br />
                        </div>
                    </div>
                }
            </Fragment>
        );
    }
}

CameraControllerSettingsView.propTypes = {
    viewModel: PropTypes.object
}