import React from 'react';
import PropTypes from 'prop-types';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button'

export default class ViewerSettingsView extends React.Component {
    constructor() {
        super();

        this.state = {};
    }

    render() {
        if (!Object.keys(this.state).length === 0 || !this.props.viewModel || !this.state.viewState)
            return null;

        return (
            <div>
                <div className='editor-sidebar-heading' style={{ fontSize: 'large' }}>
                    Project Settings
                </div>
                <div>
                    <TextField InputLabelProps={{ shrink: true }} label="Title" value={this.state.settings.project.title} style={{ width: '98%' }}
                        inputProps={{ min: 0 }} onChange={e => this.props.viewModel.onPropertyChanged('project.title', e.target.value)} />
                </div>
                <div>
                    Description
                    <textarea className='annotation-box-textinput' multiline={true} rows="3" value={this.state.settings.project.description}
                        onChange={e => this.props.viewModel.onPropertyChanged('project.description', e.target.value)} />
                </div>
                Project Image
                <img className='image-thumb' src={this.state.viewState.loadingImage} />
                <Button style={{ width: '120px' }} variant='contained' color='primary' onClick={this.props.viewModel.onCaptureScreen}>
                    Capture Image
                </Button>
                <div className='editor-sidebar-heading' style={{ fontSize: 'large' }}>
                    Viewer settings
                </div>
                <div className='editor-sidebar-heading'>
                    Auto-rotate
                </div>
                <label className="checkboxLabel">Auto Rotate
                    <input type="checkbox" checked={this.state.settings.viewer.autoRotate.enabled}
                        onChange={e => this.props.viewModel.onPropertyChanged('viewer.autoRotate.enabled', e.target.checked)} />
                    <span className="checkboxCheckmark"></span>
                </label>
                <div>
                    <TextField InputLabelProps={{ shrink: true }} label="Auto Rotate Speed" type='number' value={this.state.settings.viewer.autoRotate.speed} style={{ width: '150px' }}
                        inputProps={{ min: 0 }} onChange={e => this.props.viewModel.onPropertyChanged('viewer.autoRotate.speed', e.target.value)} />
                </div>
                <div className='editor-sidebar-heading'>
                    Menus
                </div>
                <label className="checkboxLabel">Show Options
                    <input type="checkbox" checked={this.state.settings.viewer.menu.showOptions}
                        onChange={e => this.props.viewModel.onPropertyChanged('viewer.menu.showOptions', e.target.checked)} />
                    <span className="checkboxCheckmark"></span>
                </label>
                <label className="checkboxLabel">Show Help
                    <input type="checkbox" checked={this.state.settings.viewer.menu.showHelp}
                        onChange={e => this.props.viewModel.onPropertyChanged('viewer.menu.showHelp', e.target.checked)} />
                    <span className="checkboxCheckmark"></span>
                </label>
                <label className="checkboxLabel">Show Info
                    <input type="checkbox" checked={this.state.settings.viewer.menu.showInfo}
                        onChange={e => this.props.viewModel.onPropertyChanged('viewer.menu.showInfo', e.target.checked)} />
                    <span className="checkboxCheckmark"></span>
                </label>

                <div className='editor-sidebar-heading'>
                    AR Settings
                </div>
                <label className="checkboxLabel">Enable AR
                    <input type="checkbox" checked={this.state.settings.viewer.ar.enabled}
                        onChange={e => this.props.viewModel.onPropertyChanged('viewer.ar.enabled', e.target.checked)} />
                    <span className="checkboxCheckmark"></span>
                </label>
                <label className="checkboxLabel">Use WebAR
                    <input type="checkbox" checked={this.state.settings.viewer.ar.webAr}
                        onChange={e => this.props.viewModel.onPropertyChanged('viewer.ar.webAr', e.target.checked)} />
                    <span className="checkboxCheckmark"></span>
                </label>
                <div className='editor-sidebar-heading'>
                    VR Settings
                </div>
                <label className="checkboxLabel">Enable VR
                    <input type="checkbox" checked={this.state.settings.viewer.vr.enabled}
                        onChange={e => this.props.viewModel.onPropertyChanged('viewer.vr.enabled', e.target.checked)} />
                    <span className="checkboxCheckmark"></span>
                </label>
                <TextField label="Walk speed" type='number' InputLabelProps={{ shrink: true }} value={this.state.settings.viewer.vr.walkSpeed} style={{ width: '150px' }}
                    onChange={e => this.props.viewModel.onPropertyChanged('viewer.vr.walkSpeed', e.target.value)} />
                <label className="checkboxLabel">Stop walking when outside floor geometry
                    <input type="checkbox" checked={this.state.settings.viewer.vr.rayGeometryTest}
                        onChange={e => this.props.viewModel.onPropertyChanged('viewer.vr.rayGeometryTest', e.target.checked)} />
                    <span className="checkboxCheckmark"></span>
                </label>
                {/*  <label className="checkboxLabel">Walk hit test for plane
                                <input
                                    name='vrRayPlaneTest'
                                    type="checkbox"
                                    checked={this.state.settings.viewer.vrRayPlaneTest}
                                    onChange={this.props.viewModel.onCheckedChanged} />
                                <span className="checkboxCheckmark"></span>
                            </label> */}
                {/*  <label className="checkboxLabel">Enable Teleportation
                                <input
                                    name='enableVr'
                                    type="checkbox"
                                    checked={this.state.settings.viewer.enableTeleportation}
                                    onChange={this.props.viewModel.onEnableTeleportationChange} />
                                <span className="checkboxCheckmark"></span>
                            </label> */}
                {/*  Intial VR Position
                            <div style={{ display: 'flex' }}>
                                <Button
                                    style={{ marginRight: '10px' }}
                                    variant='contained'
                                    color='primary'
                                    onClick={this.props.viewModel.onSetVRPosition}>
                                    Set to Camera
                                </Button>
                                <Button
                                    variant='contained'
                                    color='primary'
                                    onClick={this.props.viewModel.onClearVRPosition}>
                                    Clear
                                </Button>
                            </div> */}
                <div className='editor-sidebar-heading'>
                    Dimensions Tool
                </div>
                <label className="checkboxLabel">Show Dimensions Tool
                    <input type="checkbox" checked={this.state.settings.viewer.dimensionsTool.show}
                        onChange={e => this.props.viewModel.onPropertyChanged('viewer.dimensionsTool.show', e.target.checked)} />
                    <span className="checkboxCheckmark"></span>
                </label>
                <div className='editor-sidebar-heading'>
                    Animations
                </div>
                <div>
                    <TextField InputLabelProps={{ shrink: true }} label="Start Animation"
                        value={this.state.settings.viewer.animations.startAnimation} style={{ width: '250px' }}
                        onChange={e => this.props.viewModel.onPropertyChanged('viewer.animations.startAnimation', e.target.value)} />
                </div>
                <label className="checkboxLabel">Loop Start Animation
                    <input
                        type="checkbox" checked={this.state.settings.viewer.animations.loopStartAnimation}
                        onChange={e => this.props.viewModel.onPropertyChanged('viewer.animations.loopStartAnimation', e.target.checked)} />
                    <span className="checkboxCheckmark"></span>
                </label>

                <br />
                <br />
            </div>
        )
    }
}

ViewerSettingsView.propTypes = {
    viewModel: PropTypes.object,
};  