import { CommandManager } from "../commandManager";
import DeleteSelectedObjectCommand from "../commands/deleteSelectedObjectCommand";
import SetObjectTransformModeCommand from "../commands/setObjectTransformModeCommand";

export default class EditCommands {
    execute(name) {
        let cmd;

        switch (name) {
            case 'undo':
                CommandManager.undo();
                break;
            case 'redo':
                CommandManager.redo();
                break;
            case 'translate':
                cmd = new SetObjectTransformModeCommand('translate');
                break;
            case 'rotate':
                cmd = new SetObjectTransformModeCommand('rotate');
                break;
            case 'scale':
                cmd = new SetObjectTransformModeCommand('scale');
                break;
            case 'delete':
                cmd = new DeleteSelectedObjectCommand();
                break;
        }

        if(cmd)
            CommandManager.execute(cmd);
    }
}