import React from "react";
import EditorTabPanel from "../editor/editorTabPanel";
import PropTypes from 'prop-types'

export default class EditorSideBar extends React.Component {
    render() {
        return (
            <div className='editor-sidebar'>
                <EditorTabPanel tabs={this.props.tabs} onTabChanged={tab => {
                /*     // force re-render of React Portals!
                    if(tab.id === 'annotations')
                        this.flexyTextboxListRef.current.forceUpdate();  
                        this.flexyImageBoxListRef.current.forceUpdate(); */
                }} />
            </div>
        );
    }
}

EditorSideBar.propTypes = {
    tabs: PropTypes.array
}