import { InvalidArgumentError } from "../error/exceptions";

export default class Observable
{
    constructor() {
        this.observers = [];
        this.map = new Map();
    }

    subscribe(eventType, observer) {
        if(!eventType)
            throw new InvalidArgumentError('eventType');

        if(!observer)
            throw new InvalidArgumentError('observer');

        let observers = this.map.get(eventType);
        if(observers === undefined)
            observers = [];

        if(observers.indexOf(observer) > -1)
            throw Error('subscribe: already subscribed'); 
        
        observers.push(observer);
        
        this.map.set(eventType, observers);
    }

    unsubscribe(eventType, observer) {
        if(!eventType)
            throw new InvalidArgumentError('eventType');

        if(!observer)
            throw new InvalidArgumentError('observer');

        let observers = this.map.get(eventType);
        if(observers === undefined)
            return; 
        
        const index = observers.indexOf(observer);
        if (index > -1)
            this.map.set(eventType, this.observers.splice(index, 1));
    }

    isSubscribed(eventType, observer) {
        if(!eventType)
            throw new InvalidArgumentError('eventType');

        if(!observer)
            throw new InvalidArgumentError('observer');

        let observers = this.map.get(eventType);
        if(observers === undefined)
            return false; 
 
        const index = observers.indexOf(observer);
        return index > -1;
    }

    notifyAll(event) {
        if(!event)
            throw new InvalidArgumentError('event');

        const observers = this.map.get(event.constructor);
        if(observers === undefined)
            return;
        
        observers.forEach(ob => ob(event));
    }
}