import EventAggregator from "ezyvuecore/core/events/eventAggregator";
import { CommandManager } from "../commandManager";
import SetCameraPropertyCommand from "../commands/setCameraPropertyCommand";
import { UndoRedoStatusChanged } from "../editorEvents";

export default class CameraSettingsViewModel {
    constructor() {
        this.settings = {};

        this.updateViewState = this.updateViewState.bind(this);
        this.onPropertyChanged = this.onPropertyChanged.bind(this);
        this.onPropertyBlur = this.onPropertyBlur.bind(this);
        
        EventAggregator.instance.subscribe(UndoRedoStatusChanged, () => {
            this.camera && this.setCamera(this.camera);
        })
    }

    setCamera(camera) {
        this.camera = camera;

        this.settings = {
            fov: camera.fov,
            near: camera.near,
            far: camera.far
        };

        this.updateViewState();
    }

    onPropertyChanged(e) {
        this.settings[e.target.name] = e.target.value;

        this.updateViewState();
    }

    onPropertyBlur(e) {
        if (!e.key || e.key === 'Enter') {
            this.settings[e.target.name] = Number(e.target.value);

            const cmd = new SetCameraPropertyCommand(this.camera, e.target.name, Number(e.target.value));
          
            CommandManager.execute(cmd, );

            this.updateViewState();
        }
    }

    updateViewState(view = null) {
        if (view)
            this.view = view;

        if (this.view && this.camera)
            this.view.setState({ ...this.settings });   
    }
}