import React from "react";
import PropTypes from 'prop-types';
import Tooltip from '@material-ui/core/Tooltip';
import { Fragment } from "react";
import TextField from '@material-ui/core/TextField';

export default class LightSettingsView extends React.Component {
    constructor() {
        super();

        this.state = {};
    }

    render() {
        if(!this.props.viewModel || Object.keys(this.state).length === 0 || !this.props.viewModel.light)
            return;

        return (
            <Fragment>
                <div className='flexrow'>
                    <div className='flexcol'>
                        Color
                    </div>
                    <div className='flexcol'>
                        <Tooltip title="Select Color">
                            <input type='color'name='color' value={this.state.color} onChange={this.props.viewModel.onPropChanged} />
                        </Tooltip>
                    </div>
                </div>
                <div>
                    Intensity&nbsp;
                    <TextField
                        value={this.state.intensity}
                        name='intensity'
                        type='number'
                        style={{ width: '100px', marginRight: '2px' }}
                        onChange={this.props.viewModel.onPropChanged} />
                </div>
                {
                    this.state.isSpotlight &&
                    <div>
                    Spot Angle&nbsp;
                    <TextField
                        value={this.state.angle}
                        name='angle'
                        type='number'
                        style={{ width: '100px', marginRight: '2px' }}
                        onChange={this.props.viewModel.onPropChanged} />
                </div>
                }
            </Fragment>
        );
    }
}

LightSettingsView.propTypes = {
    viewModel: PropTypes.object
};