import React, { Fragment } from 'react'
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button';
import Checkbox from '@material-ui/core/Checkbox';
import Tooltip from '@material-ui/core/Tooltip';
import TextField from '@material-ui/core/TextField';

export default class AnnotationsView extends React.Component {
    constructor() {
        super();

        this.state = {};
    }

    render() {
        if(!this.props.viewModel || Object.keys(this.state).length === 0)
            return;

        return (
            <Fragment>
                <div className='flexrow'>
                    <div style={{ width: '250px' }}>
                        Occlusion Test
                    </div>
                    <div>
                        <Checkbox
                            checked={this.state.modelState.visibilityTest}
                            onChange={(e, value) => this.props.viewModel.onCommitProperty('visibilityTest', value)} />
                    </div>
                </div>
                <div className='flexrow'>
                    <div style={{ width: '250px' }}>
                        Show Nav Buttons
                    </div>
                    <div>
                        <Checkbox
                            checked={this.state.modelState.showNavButtons}
                            onChange={(e, value) => this.props.viewModel.onCommitProperty('showNavButtons', value)} />
                    </div>
                </div>
                <div className='editor-sidebar-heading'>
                    Button Properties        
                </div>
                <div>
                    Size&nbsp;
                    <TextField
                        value={this.state.modelState.size}
                        name='size'
                        type='number'
                        style={{ width: '60px', marginRight: '2px' }}
                        onBlur={this.props.viewModel.onPropertyBlur}
                        onKeyUp={this.props.viewModel.onPropertyBlur}
                        onChange={this.props.viewModel.onPropertyChanged} />
                </div>
                <div className='flexrow'>
                    <div className='flexcol'>
                        Text Color
                    </div>
                    <div className='flexcol'>
                        <Tooltip title="Select Color">
                            <input
                                type='color'
                                value={this.state.modelState.textColor}
                                onChange={e => this.props.viewModel.onCommitProperty('textColor', e.target.value)} />
                        </Tooltip>
                    </div>
                </div>
                <div className='flexrow'>
                    <div className='flexcol'>
                        Fill Color
                    </div>
                    <div className='flexcol'>
                        <Tooltip title="Select Color">
                            <input
                                type='color'
                                value={this.state.modelState.fillColor}
                                name='backgroundColor'
                                onChange={e => this.props.viewModel.onCommitProperty('fillColor',  e.target.value)} />
                        </Tooltip>
                    </div>
                </div>
                <div className='flexrow'>
                    <div className='flexcol'>
                        Border Color
                    </div>
                    <div className='flexcol'>
                        <Tooltip title="Select Color">
                            <input
                                type='color'
                                value={this.state.modelState.borderColor}
                                name='borderColor'
                                onChange={e => this.props.viewModel.onCommitProperty('borderColor',  e.target.value)} />
                        </Tooltip>
                    </div>
                </div>
                
                <div className='editor-sidebar-heading'>
                    Add Annotation        
                </div>
                <div>
                    Button Text&nbsp;
                    <TextField
                        value={this.state.viewState.buttonText}
                        style={{ width: '100px', marginRight: '2px' }}
                        onChange={e => this.props.viewModel.onViewStatePropertyChanged('buttonText', e.target.value)} />
                </div>
                <div>
                    Label Text
                    <textarea className='annotation-box-textinput' multiline={true} rows="3" value={this.state.viewState.labelText}
                        onChange={e => this.props.viewModel.onViewStatePropertyChanged('labelText', e.target.value)} />
                    {
                        this.state.viewState.editMode === 'add' ?
                            <div>
                                <div>
                                    Click on the model to position the label. Rotate, zoom and pan the camera to adjust the view.
                                </div>
                                <Button variant='contained' color='primary' size='small'
                                    disabled={this.state.viewState.editMode !== 'edit'} onClick={this.props.viewModel.onEditModeOk}>
                                    Ok
                                </Button>
                            </div>
                            :
                            <Button variant='contained' color='primary' size='small' disabled={this.state.viewState.addButtonDisabled}
                                onClick={this.props.viewModel.onAddAnnotation}>
                                Add
                            </Button>
                    }
                </div>
            </Fragment>
        );
    }
}

AnnotationsView.propTypes = {
    viewModel: PropTypes.object
};