import EditorTab from "members/features/editor/editorTab";
import Tooltip from '@material-ui/core/Tooltip';
import React from "react";
import SvgIcon from '@material-ui/core/SvgIcon';
import { ReactComponent as CubeIcon } from 'assets/icons/cube.svg';
import Object3dPropertiesView from "../views/object3dPropertiesView";
import Object3dPropertiesViewModel from "../viewModels/object3dPropertiesViewModel";
import { Fragment } from "react";

export default class ObjectPropertiesTab extends EditorTab {
    constructor() {
        super();

        this.viewModel = new Object3dPropertiesViewModel();
    }

    renderButtonIcon() {
        return (
            <SvgIcon component={CubeIcon} viewBox="0 0 121.48 122.88" style={{ width: '24px' }} />
        )
    }

    renderContent() {
        return (
            <Fragment>
                <div className='editor-sidebar-heading' style={{ fontSize: 'large' }}>
                    Object Properties
                </div>
                <Object3dPropertiesView ref={this.viewModel.updateViewState} viewModel={this.viewModel} />
            </Fragment>
        );
    }
}