import Command from "../command";
import { Editor } from "../editor";

export default class SetRotationCommand extends Command {
    constructor(object, rot, initialRot) {
        super();

        this.object = object;
        this.rot = rot;
        this.initialRot = initialRot;
    }

    get name() {
        return 'Set Rotation'
    }

    execute() {
        Editor.setObjectRotation(this.object, this.rot);
    }

    undo() {
        Editor.setObjectRotation(this.object, this.initialRot);
    }

    redo() {
        this.execute();
    }
}