export default class AnimationsViewModel {
    constructor() {
        this.clipsModel = { clips: [] };
        this.playingCount = 0;

        this.updateViewState = this.updateViewState.bind(this);
        this.playClip = this.playClip.bind(this);
        this.stopClip = this.stopClip.bind(this);
    }

    setObject(object) {
        this.object = object;

        this.clipsModel.clips = [];

        if(object.animationPlayer) {
            this.clips = object.animations;
            if (this.clips && this.clips.length) {
                this.currentClip = this.clips[0];

                this.clipsModel = {
                    clips: this.clips.map(clip => ({
                        name: clip.name,
                        duration: clip.duration,
                        playing: false
                    }))
                };
            }
        }
        
        this.updateViewState();
    }

    update(delta) {
       this.object.animationPlayer.update(delta);
    }

    playClip(index) {
        this.stopAll();

       this.object.animationPlayer.playClip(this.clips[index], this.clipsModel.clips[index].loop);

        this.clipsModel.clips[index].playing = true;

        ++this.playingCount;

        this.updateViewState();
    }

    playClipByName(name) {
        this.stopAll();

        const index = this.clips.findIndex(clip => clip.name === name);
        if (index !== -1)
            this.playClip(index);
    }

    stopAll() {
        this.clips.forEach((clip, i) => {
            this.stopClip(i);
        });
    }

    stopClip(index) {
       this.object.animationPlayer.stopClip(this.clips[index]);

        this.clipsModel.clips[index].playing = false;

        --this.playingCount;

        this.updateViewState();
    }

    toggleLoop(index) {
        this.clipsModel.clips[index].loop = !this.clipsModel.clips[index].loop;

        this.updateViewState();
    }


    updateViewState(view = null) {
        if (view)
            this.view = view;

        if (this.view)
            this.view.setState({
                clipsState: { ...this.clipsModel },
            });
    }
}