import { AnimationClip } from "ezyvuecore/three/build/three";
import { GLTFExporter } from "ezyvuecore/three/examples/jsm/exporters/GLTFExporter";
import { zipSync } from "ezyvuecore/three/examples/jsm/libs/fflate.module";
import { Edit } from "../../../../node_modules/@material-ui/icons/index";
import { Editor } from "./editor";

export default class ProjectExporter {
    _save(blob, filename) {
        const link = document.createElement('a');

        link.href = URL.createObjectURL(blob);
        link.download = filename || 'data.json';
        link.dispatchEvent(new MouseEvent('click'));
    }

    serializeAnimations(scene) {
        scene.traverse(o => {
            if (o.animations) {
                o.userData.animations = [];

                o.animations.forEach(clip => {
                    o.userData.animations.push(AnimationClip.toJSON(clip));
                })
            }
        });
    }

    removeLightChildNodes(scene) {
        scene.traverse(o => {
            if (o.isLight)
                o.clear();
        });
    }

    async zipProject(project, exportGlb) {
        const files = {}
        const exporter = new GLTFExporter();

        this.serializeAnimations(project.scene);

        this.removeLightChildNodes(project.scene);

        if (exportGlb) {
            files['scene.glb'] = await new Promise((resolve, reject) => {
                exporter.parse(project.scene, result => { resolve(new Uint8Array(result)); }, err => { reject(err) }, { binary: true });
            });
        }

        if (project.envMap && project.envMap.dataUrl) {
            const resp = await fetch(project.envMap.dataUrl);
            
            files['envmap.hdr'] = new Uint8Array(await resp.arrayBuffer());
        }

        files['settings.json'] = new Uint8Array(new TextEncoder().encode(JSON.stringify(project.settings.state)).buffer);

        return zipSync(files, { level: 6 });
    }

    async export(fn, project, exportGlb = true) {
        const zipped = await this.zipProject(project, exportGlb);

        this._save(new Blob([zipped], { type: 'application/octet-stream' }), fn);
    }
}