import { getAllMeshes } from 'ezyvuecore/engine/three/util';
import { Raycaster, Vector2 } from 'ezyvuecore/three/build/three';

export class ObjectSelector
{
    constructor() {
        this.rayCaster = new Raycaster();
        this.vec2 = new Vector2();
    }
    
    intersectionTest(scene, camera, clickEvent) {
        this.vec2.set(clickEvent.offsetX / clickEvent.target.clientWidth * 2.0 - 1.0, -clickEvent.offsetY / clickEvent.target.clientHeight * 2.0 + 1.0);
        this.rayCaster.setFromCamera( this.vec2, camera );       
        
        return this.rayCaster.intersectObjects(getAllMeshes(scene));
    }
}