import EditorTab from "members/features/editor/editorTab";
import Tooltip from '@material-ui/core/Tooltip';
import React from "react";
import SceneGraphView from "../views/sceneGraphView";
import { SceneGraphViewModel } from "../viewModels/sceneGraphViewModel";
import LandscapeIcon from '@material-ui/icons/Landscape';
import EnvironmentSettingsView from "../views/environmentSettingsView";
import { Fragment } from "react";
import EnvironmentSettingsViewModel from "../viewModels/environmentSettingsViewModel";

export default class SceneTab extends EditorTab {
    constructor() {
        super();

        this.onClick = this.onClick.bind(this);

        this.viewModel = new SceneGraphViewModel();
        this.environmentSettingsViewModel = new EnvironmentSettingsViewModel();

        this.environmentSettingsViewModel.updateViewState();
    }

    onClick() {
        this.environmentSettingsViewModel.updateViewState();
    }

    renderButtonIcon() {
        return (
            <LandscapeIcon />
        )
    }

    renderContent() {
        return (
            <Fragment>
                <div className='editor-sidebar-heading' style={{ fontSize: 'large' }}>
                    Scene Properties
                </div>
                <SceneGraphView viewModel={this.viewModel} ref={this.viewModel.updateViewState} />
                <EnvironmentSettingsView ref={this.environmentSettingsViewModel.updateViewState} viewModel={this.environmentSettingsViewModel} />
            </Fragment>
        );
    }
}