import EditorTab from "members/features/editor/editorTab";
import Tooltip from '@material-ui/core/Tooltip';
import React from "react";
import { Fragment } from "react";
import ViewerSettingsView from "../views/viewerSettingsView";
import ViewerSettingsViewModel from "../viewModels/viewerSettingsViewModel";
import CropOriginalIcon from '@material-ui/icons/CropOriginal';

export default class ViewerTab extends EditorTab {
    constructor() {
        super();

        this.viewModel = new ViewerSettingsViewModel();
    }

    renderButtonIcon() {
        return (
            <CropOriginalIcon />
        )
    }

    renderContent() {
        return (
            <Fragment>
                <ViewerSettingsView viewModel={this.viewModel} ref={this.viewModel.updateViewState} />
            </Fragment>
        );
    }
}