import { cloneDeep } from "lodash";
import Command from "../command";
import { Editor } from "../editor";

export default class SetAnnotationsPropertyCommand extends Command {
    constructor(settings, prevSettings) {
        super();

        this.settings = cloneDeep(settings);
        this.prevSettings = cloneDeep(prevSettings)
    }

    get name() {
        return 'Set Environment Property';
    }

    execute() {
        Editor.project.settings.state = { annotations: this.settings };

        Editor.updateAnnotations();
    }

    undo() {
        Editor.project.settings.state = { annotations: this.prevSettings };

        Editor.updateAnnotations();
    }

    redo() {
        this.execute();
    }
}