import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import PropTypes from 'prop-types'
import WarningIcon from '@material-ui/icons/Warning';

export default function AlertResponseDialog(props) {
    return (
        <Dialog
            fullWidth={false}
            maxWidth="md"
            open={props.open}>
            <DialogContent>
                <div style={{ color: 'red', display: 'flex', alignItems: 'center' }} >
                    <WarningIcon fontSize='large' color='error' />&nbsp;Alert
                </div>
                <div style={{ color: 'red', paddingTop: '2   0px' }} dangerouslySetInnerHTML={{ __html: props.message }}>
                </div>
            </DialogContent>
            <DialogActions>
                <Button onClick={props.onOk}>
                    <span style={{ color: 'red' }}>{props.okText}</span>
                </Button>
                <Button style={{ color: 'green' }} onClick={props.onCancel}>
                    {props.cancelText}
                </Button>
            </DialogActions>
        </Dialog>
    );
}

AlertResponseDialog.defaultProps = {
    okText: 'OK',
    cancelText: 'CANCEL'
}

AlertResponseDialog.propTypes = {
    open: PropTypes.bool.isRequired,
    message: PropTypes.string.isRequired,
    onOk: PropTypes.func.isRequired,
    onCancel: PropTypes.func.isRequired,
    okText: PropTypes.string,
    cancelText: PropTypes.string,
};