import { DirectionalLight, HemisphereLight, PointLight, SpotLight } from "ezyvuecore/three/build/three";
import Command from "../command";
import { Editor } from "../editor";

export default class AddLightCommand extends Command {
    constructor(type) {
        super();

        this.type = type;
    }

    get name() {
        return 'Add ' + this.type;
    }

    execute() {
        switch(this.type) {
            case 'DirectionalLight':
                this.light = Editor.addDirectionalLight();
                break;
            case 'PointLight':
                this.light = Editor.addPointLight();
                break;
            case 'SpotLight':
                this.light = Editor.addSpotLight();
                break;
            case 'HemisphereLight':
                this.light = Editor.addHemisphereLight();
                break;
        }

        if(this.light) 
            Editor.selectObject(this.light);
    }

    undo() {
        Editor.removeObject(this.object);
    }

    redo() {
        Editor.addObject(this.object);
    }
}