import React from "react";
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import PropTypes from 'prop-types';
import EventAggregator from "ezyvuecore/core/events/eventAggregator";
import { ObjectDeseletedEvent, ObjectSelectedEvent, UndoRedoStatusChanged } from "../editorEvents";
import { CommandManager } from "../commandManager";
import Divider from '@material-ui/core/Divider';
import { ObjectDeletedEvent } from "common/applicationEvents";

export default class EditMenu extends React.PureComponent {
    constructor(props) {
        super(props);

        this.executeCommand = this.executeCommand.bind(this);

        this.state = {
            nextUndoCmd: null,
            nextRedoCmd: null,
            deleteEnabled: false
        };

        EventAggregator.instance.subscribe(UndoRedoStatusChanged, () => {
            this.setState({
                nextUndoCmd: CommandManager.nextUndoCommand,
                nextRedoCmd: CommandManager.nextRedoCommand
            });
        });

        EventAggregator.instance.subscribe(ObjectSelectedEvent, () => {
            this.setState({ deleteEnabled: true });
        })

        EventAggregator.instance.subscribe(ObjectDeseletedEvent, () => {
            this.setState({ deleteEnabled: false });
        })
    }

    executeCommand(name) {
        this.props.editCommands && this.props.editCommands.execute(name);
        this.props.onClose();
    }

    render() {
        return (
            <Menu
                anchorEl={this.props.anchorElem}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'right',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'center',
                }}
                keepMounted
                open={Boolean(this.props.anchorElem && !this.props.disabled)}
                onClose={this.props.onClose} >
                <MenuItem onClick={() => this.executeCommand('translate')}>
                    Translate
                </MenuItem>
                <MenuItem onClick={() => this.executeCommand('rotate')}>
                    Rotate
                </MenuItem>
                <MenuItem onClick={() => this.executeCommand('scale')}>
                    Scale
                </MenuItem>
                <Divider />
                <MenuItem disabled={!this.state.deleteEnabled} onClick={() => this.executeCommand('delete')}>
                    Delete
                </MenuItem>
                <Divider />
                <MenuItem disabled={!this.state.nextUndoCmd} onClick={() => this.executeCommand('undo')}>
                    {'Undo ' + (this.state.nextUndoCmd?.name ?? '')}
                </MenuItem>
                <MenuItem disabled={!this.state.nextRedoCmd} onClick={() => this.executeCommand('redo')}>
                    {'Redo ' + (this.state.nextRedoCmd?.name ?? '')}
                </MenuItem>
            </Menu>
        );
    }
}

EditMenu.propTypes = {
    anchorElem: PropTypes.object,
    disabled: PropTypes.bool,
    onClose: PropTypes.func,
    editCommands: PropTypes.object,
}