import React from 'react';
import PropTypes from 'prop-types';
import TextField from '@material-ui/core/TextField';
import Tooltip from '@material-ui/core/Tooltip';

export default class EditorSettingsView extends React.Component {
    constructor() {
        super();

        this.state = {};
    }

    render() {
        if (!this.props.viewModel || Object.keys(this.state).length === 0)
            return null;

        return (
            <div>
                <div className='editor-sidebar-heading'>
                    Grid
                </div>
                <div>
                    <label className="checkboxLabel">Show
                        <input type="checkbox" checked={this.state.viewState.gridOn}
                            onChange={e => this.props.viewModel.onPropertyChanged('gridOn', e.target.checked)} />
                        <span className="checkboxCheckmark"></span>
                    </label>
                </div>
                <div>
                    <TextField label="Size" InputLabelProps={{ shrink: true }}
                        value={this.state.viewState.gridSize} style={{ width: '200px' }}
                        onChange={e => this.props.viewModel.onPropertyChanged('gridSize', e.target.value)} />
                </div>
                <div>
                    <TextField label="Divisions" InputLabelProps={{ shrink: true }}
                        value={this.state.viewState.gridDivisions} style={{ width: '200px' }}
                        onChange={e => this.props.viewModel.onPropertyChanged('gridDivisions', e.target.value)} />
                </div>
                <div className='flexrow'>
                    <div className='flexcol'>
                        Line Color
                    </div>
                    <div className='flexcol'>
                        <Tooltip title="Select Color">
                            <input type='color' value={this.state.viewState.gridColor} 
                                onChange={e => this.props.viewModel.onPropertyChanged('gridColor', e.target.value)} />
                        </Tooltip>
                    </div>
                </div>
                <div className='flexrow'>
                    <div className='flexcol'>
                        Center Line Color
                    </div>
                    <div className='flexcol'>
                        <Tooltip title="Select Color">
                            <input type='color' value={this.state.viewState.gridCenterColor} 
                                onChange={e => this.props.viewModel.onPropertyChanged('gridCenterColor', e.target.value)} />
                        </Tooltip>
                    </div>
                </div>
            </div>
        )
    }
}

EditorSettingsView.propTypes = {
    viewModel: PropTypes.object,
};  