import EditorTab from "members/features/editor/editorTab";
import Tooltip from '@material-ui/core/Tooltip';
import React, { Fragment } from "react";
import AnnotationsView from "../views/annotationsView";
import AnnotationsViewModel from "../viewModels/annotationsViewModel";
import TextFieldsIcon from '@material-ui/icons/TextFields';

export default class AnnotationsTab extends EditorTab {
    constructor() {
        super();

        this.viewModel = new AnnotationsViewModel();
    }

    renderButtonIcon() {
        return (
            <TextFieldsIcon />
        )
    }

    renderContent() {
        return (
            <Fragment>
                <div className='editor-sidebar-heading' style={{ fontSize: 'large' }}>
                    Annotation Properties
                </div>
                <AnnotationsView viewModel={this.viewModel} ref={this.viewModel.updateViewState} />
            </Fragment>
        );
    }
}