import { EquirectangularReflectionMapping } from "ezyvuecore/three/build/three";
import Command from "../command";
import { Editor } from "../editor";
import EventAggregator from "ezyvuecore/core/events/eventAggregator";
import { ProjectChangedEvent } from "../editorEvents";

export default class SetEnvironmentMapCommand extends Command {
    constructor(texture) {
        super();

        this.prevTexture = Editor.world.scene.environment;
        this.texture = texture;
    }

    get name() {
        return 'Set Environment Map';
    }

    execute() {
        if(this.texture)
            this.texture.mapping = EquirectangularReflectionMapping;
                
        Editor.project.envMap = this.texture;

        Editor.world.setEnvironmentMap(this.texture);
    }

    undo() {
        Editor.project.envMap = this.prevTexture;

        Editor.world.setEnvironmentMap(this.prevTexture);
     }

    redo() {
        this.execute();   
    }
}