import SetObjectPropertyCommand from "./setObjectPropertyCommand";

export default class SetCameraPropertyCommand extends SetObjectPropertyCommand {
    constructor(object, propName, value) {
        super(object, propName, value);
    }
    
    execute() {
        super.execute();

        this.object.updateProjectionMatrix();
    }

    undo() {
        super.undo();

        this.object.updateProjectionMatrix();
    }
}