import Command from "../command";
import { Editor } from "../editor";

export default class SetScaleCommand extends Command {
    constructor(object, scale, initialScale) {
        super();

        this.object = object;
        this.scale = scale;
        this.initialScale = initialScale;
    }

    get name() {
        return 'Set Scale'
    }

    execute() {
        Editor.setObjectScale(this.object, this.scale);
    }

    undo() {
        Editor.setObjectScale(this.object, this.initialScale);
    }

    redo() {
        this.execute();
    }
}