
import EventAggregator from "ezyvuecore/core/events/eventAggregator";
import { ErrorEvent } from "common/applicationEvents";
import { convertFiles } from "../convertFiles";
import Command from "../command";
import { Editor } from "../editor";

export default class ImportDirCommand extends Command {
    constructor(files) {
        super();

        this.files = Array.from(files);
    }

    get name() {
        return 'Import Folder'
    }

    execute() {
        convertFiles(Editor.world.renderer.renderer, this.files, scene => {
            Editor.addObject(scene);
        },
        err => {
            EventAggregator.instance.notifyAll(new ErrorEvent(`Error importing file - ${err}`))
        });
    }

    get undoable() { return false; }
}