import React from "react";

export default class EditorTab {
    constructor(id) {
        this._id = id;
    }

    renderButton(isActive, onButtonClick, index) {
        return (
            <button
                key={index}
                className={isActive ? 'active' : ''}
                onClick={() => onButtonClick && onButtonClick()}>
                {
                    this.renderButtonIcon()
                }
            </button>
        );
    }

    get id() {
        return this._id;
    }

    renderContent() { return null; }
}
