import EditorTab from "members/features/editor/editorTab";
import Tooltip from '@material-ui/core/Tooltip';
import React from "react";
import WbSunnyIcon from '@material-ui/icons/WbSunny';
import { Fragment } from "react";
import LightingSettingsViewModel from "../viewModels/lightingSettingsViewModel";
import LightingSettingsView from "../views/lightingSettingsView";

export default class LightingTab extends EditorTab {
    constructor() {
        super();
        this.viewModel = new LightingSettingsViewModel();
    }

    renderButtonIcon() {
        return (
            <WbSunnyIcon />
        )
    }

    renderContent() {
        return (
            <Fragment>
                <div className='editor-sidebar-heading' style={{ fontSize: 'large' }}>
                    Lighting Properties
                </div>
                <LightingSettingsView viewModel={this.viewModel} ref={this.viewModel.updateViewState} />
            </Fragment>
        );
    }
}