import React from "react";
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import PropTypes from 'prop-types';
import Divider from '@material-ui/core/Divider';

export default class AddMenu extends React.PureComponent {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <Menu
                anchorEl={this.props.anchorElem}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'right',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'center',
                }}
                keepMounted
                open={Boolean(this.props.anchorElem && !this.props.disabled)}
                onClose={this.props.onClose} >
                <MenuItem onClick={
                    () => {
                        this.props.addCommands && this.props.addCommands.execute('primitive.Sphere');
                        this.props.onClose();
                    }}>
                    Sphere
                </MenuItem>
                <MenuItem onClick={
                    () => {
                        this.props.addCommands && this.props.addCommands.execute('primitive.Box');
                        this.props.onClose();
                    }}>
                    Box
                </MenuItem>
                <MenuItem onClick={
                    () => {
                        this.props.addCommands && this.props.addCommands.execute('primitive.Cylinder');
                        this.props.onClose();
                    }}>
                    Cylinder
                </MenuItem>
                <MenuItem onClick={
                    () => {
                        this.props.addCommands && this.props.addCommands.execute('primitive.Torus');
                        this.props.onClose();
                    }}>
                    Torus
                </MenuItem>
                <MenuItem onClick={
                    () => {
                        this.props.addCommands && this.props.addCommands.execute('primitive.Capsule');
                        this.props.onClose();
                    }}>
                    Capsule
                </MenuItem>
                <MenuItem onClick={
                    () => {
                        this.props.addCommands && this.props.addCommands.execute('primitive.Tetrahedron');
                        this.props.onClose();
                    }}>
                    Tetrahedron
                </MenuItem>
                <MenuItem onClick={
                    () => {
                        this.props.addCommands && this.props.addCommands.execute('primitive.Sphere');
                        this.props.onClose();
                    }}>
                    Sphere
                </MenuItem>
                <MenuItem onClick={
                    () => {
                        this.props.addCommands && this.props.addCommands.execute('primitive.Icosahedron');
                        this.props.onClose();
                    }}>
                    Icosahedron
                </MenuItem>
                <MenuItem onClick={
                    () => {
                        this.props.addCommands && this.props.addCommands.execute('primitive.Tetrahedron');
                        this.props.onClose();
                    }}>
                    Tetrahedron
                </MenuItem>
                <MenuItem onClick={
                    () => {
                        this.props.addCommands && this.props.addCommands.execute('primitive.Dodecahedron');
                        this.props.onClose();
                    }}>
                    Dodecahedron
                </MenuItem>
                <MenuItem onClick={
                    () => {
                        this.props.addCommands && this.props.addCommands.execute('primitive.Lathe');
                        this.props.onClose();
                    }}>
                    Lathe
                </MenuItem>
                <MenuItem onClick={
                    () => {
                        this.props.addCommands && this.props.addCommands.execute('primitive.Plane');
                        this.props.onClose();
                    }}>
                    Plane
                </MenuItem>
                <MenuItem onClick={
                    () => {
                        this.props.addCommands && this.props.addCommands.execute('primitive.Circle');
                        this.props.onClose();
                    }}>
                    Circle
                </MenuItem>
                <MenuItem onClick={
                    () => {
                        this.props.addCommands && this.props.addCommands.execute('primitive.Ring');
                        this.props.onClose();
                    }}>
                    Ring
                </MenuItem>
                <MenuItem onClick={
                    () => {
                        this.props.addCommands && this.props.addCommands.execute('primitive.Tube');
                        this.props.onClose();
                    }}>
                    Tube
                </MenuItem>
                <Divider />
                <MenuItem onClick={
                    () => {
                        this.props.addCommands && this.props.addCommands.execute('light.PointLight');
                        this.props.onClose();
                    }}>
                    Point Light
                </MenuItem>
                <MenuItem onClick={
                    () => {
                        this.props.addCommands && this.props.addCommands.execute('light.DirectionalLight');
                        this.props.onClose();
                    }}>
                    Directional Light
                </MenuItem>
                <MenuItem onClick={
                    () => {
                        this.props.addCommands && this.props.addCommands.execute('light.SpotLight');
                        this.props.onClose();
                    }}>
                    Spotlight
                </MenuItem>
                <MenuItem onClick={
                    () => {
                        this.props.addCommands && this.props.addCommands.execute('light.HemisphereLight');
                        this.props.onClose();
                    }}>
                    Hemisphere Light
                </MenuItem>
            </Menu>
        );
    }
}

AddMenu.propTypes = {
    anchorElem: PropTypes.object,
    onClose: PropTypes.func,
    addCommands: PropTypes.object
}