import { BufferGeometry, Float32BufferAttribute, Light, Line, LineBasicMaterial, LineSegments, Object3D, Sprite, Vector3 } from "ezyvuecore/three/build/three";

const _v1 = new Vector3();
const _v2 = new Vector3();
const _v3 = new Vector3();

class LightHelper extends Object3D {
	constructor(light, texture) {
		super();

		this.light = light;

		this.sprite = new Sprite();
		this.sprite.material.map = texture;
		this.sprite.material.sizeAttenuation = false;

		this.sprite.scale.set(0.05, 0.05, 0.05);

		this.add(this.sprite);

		this.isLightHelper = true;

		this.matrix = this.light.matrixWorld;

		this.matrixAutoUpdate = false;
	}

	update() {
		this.sprite.material.color.copy( this.light.color );
	}

	dispose() {
		this.sprite.geometry.dispose();
		this.sprite.material.dispose();
	}
}

export class PointLightHelper extends LightHelper {
	constructor(light, texture) {
		super(light, texture);
	}
}

export class DirectionalLightHelper extends LightHelper {
	constructor(light, texture) {
		super(light, texture);

		let geometry = new BufferGeometry();
		geometry.setAttribute('position', new Float32BufferAttribute([0, 0, 0, 0, 0, 0.25], 3));

		const material = new LineBasicMaterial({ fog: false, toneMapped: false });

		this.targetLine = new Line(geometry, material);
		this.add(this.targetLine);
	}

	update() {
		super.update();

		this.targetLine.material.color.copy( this.light.color );
		
		this.light.updateWorldMatrix( true, false );
		this.light.target.updateWorldMatrix( true, false );

		_v1.setFromMatrixPosition( this.light.matrixWorld );
		_v2.setFromMatrixPosition( this.light.target.matrixWorld );
		_v3.subVectors( _v2, _v1 );

		if ( this.color !== undefined ) {
			this.targetLine.material.color.set( this.color );

		} else {
			this.targetLine.material.color.copy( this.light.color );

		}

		this.targetLine.lookAt( _v2 );
		this.targetLine.scale.z = _v3.length();
	}

	dispose() {
		super.dispose();

		this.targetLine.geometry.dispose();
		this.targetLine.material.dispose();
	}
}

export class SpotLightHelper extends LightHelper  {
	constructor( light, texture) {
		super(light, texture);

		const geometry = new BufferGeometry();

		const size = 0.1;

		const positions = [
			0, 0, 0, 	0, 0, size * 2,
			0, 0, 0, 	size, 0, size,
			0, 0, 0,	-size, 0, size,
			0, 0, 0, 	0, size, size,
			0, 0, 0, 	0, -size, size
		];

		for ( let i = 0, j = 1, l = 32; i < l; i ++, j ++ ) {

			const p1 = ( i / l ) * Math.PI * 2;
			const p2 = ( j / l ) * Math.PI * 2;

			positions.push(
				Math.cos( p1 ) * size, Math.sin( p1 ) * size, size,
				Math.cos( p2 ) * size, Math.sin( p2 ) * size, size
			);

		}

		geometry.setAttribute( 'position', new Float32BufferAttribute( positions, 3 ) );

		const material = new LineBasicMaterial( { fog: false, toneMapped: false } );

		this.cone = new LineSegments( geometry, material );
		this.add( this.cone );

		this.update();

	}

	dispose() {
		super.dispose();

		this.cone.geometry.dispose();
		this.cone.material.dispose();
	}

	update() {
		this.light.updateWorldMatrix( true, false );
		this.light.target.updateWorldMatrix( true, false );

		const coneLength = this.light.distance ? this.light.distance : 2;
		const coneWidth = coneLength * Math.tan( this.light.angle );

		this.cone.scale.set( coneWidth, coneWidth, coneLength );

		_v1.setFromMatrixPosition( this.light.target.matrixWorld );

		this.cone.lookAt( _v1 );

		this.cone.material.color.copy( this.light.color );
	}
}
