import React from "react";
import EditorView from "./editorView";
import EditorViewModel from "./editorViewModel";

export default class EzyvueStudio extends React.Component {
    constructor(props) {
        super(props);

        this.canvasContainerRef = React.createRef();
        this.canvasRef = React.createRef();
        this.tabs = [];
    }

    componentDidMount() {
        this.viewModel = new EditorViewModel();

        this.viewModel.updateViewState();

        this.forceUpdate();
    }
 
    render() {
        if(!this.viewModel)
            return null;
            
        return (
            <EditorView 
                ref={this.viewModel.updateViewState} 
                viewModel={this.viewModel}
                containerRef={this.canvasContainerRef} 
                canvasRef={this.canvasRef} tabs={this.tabs}  />
        )
    }
}