import FileConverter from "./fileConverter";
import { modelFileExts } from "./fileExtensions";
import GltfLoader from "ezyvuecore/engine/three/gltfLoader";

function getModelFile(files) {
    return files.find(file => {
        return modelFileExts.includes('.' + file.name.split('.').pop().toLowerCase());
    });
}

export async function convertFiles(renderer, files, onLoad, onError) {
    let modelFile = getModelFile(files);
    if (!modelFile)
        throw new Error('no valid model file found');

    if (modelFile.name.split('.').pop().toLowerCase() === 'glb') {
        const reader = new FileReader();

        reader.addEventListener('load', async event => {
            const buffer = event.target.result;

            const loader = new GltfLoader(renderer);
            const gltf = await loader.parse(buffer, onError);
            
            gltf.scene.animations = gltf.animations;
            
            onLoad(gltf.scene);
        });

        reader.readAsArrayBuffer(modelFile);
    }
    else {
        const fileConverter = new FileConverter();

        const buffer = await fileConverter.convertToGLB(files, undefined, err => {
            throw new Error(`Failed to convert file, error: ${err}. Contact support if this error persists`);
        });

        const loader = new GltfLoader(renderer);
        const gltf = await loader.parse(buffer, onError);
        
        gltf.scene.animations = gltf.animations;
            
        onLoad(gltf.scene);
    }
}