import Command from "../command";
import { Editor } from "../editor";
import ProjectExporter from "../projectExporter";

export default class ExportProjectCommand extends Command {
    constructor() {
        super();
    }

    async execute() {
        Editor.deleteAllBakedShadowMeshes();

        const pickerOpts = {
            types: [
                {
                    description: "Zip Files",
                    accept: {
                        "application/zip": [".zip"],
                    },
                },
            ],
        };

        const zipped = await new ProjectExporter().zipProject(Editor.project, true);

        try {
            const fileHandle = await window.showSaveFilePicker(pickerOpts);

            const writable = await fileHandle.createWritable();

            await writable.write(zipped);
            await writable.close();
        }
        catch (e) { }

        Editor.createAllBakedShadows();
    }

    get undoable() { return false; }
}