import Command from "../command";
import { Editor } from "../editor";

export default class FrameSelectionCommand extends Command {
    constructor() {
        super();
    }

    get name() {
        return 'Frame Selection';
    }

    get undoable() { return false; }

    execute() {
        if(Editor.selectedObject)
            Editor.frameObject(Editor.selectedObject);
    }
}