import Command from "../command";
import { Editor } from "../editor";

export default class DeleteSelectedObjectCommand extends Command {
    constructor() {
        super();
    }

    get name() {
        return 'Delete Object'
    }

    execute() {
        this.object = Editor.selectedObject;
        if(this.object)
            Editor.removeObject(this.object);
    }

    undo() {
        if(this.object)
            Editor.addObject(this.object);
    }

    redo() {
        this.execute();
    }
}