
import EventAggregator from "ezyvuecore/core/events/eventAggregator";
import Command from "../command";
import { ErrorEvent } from "common/applicationEvents";
import Project from "../project";
import ProjectImporter from "../projectImporter";
import { Editor } from "../editor";
import { ProjectChangedEvent } from "../editorEvents";

export default class ImportProjectCommand extends Command {
    constructor(file) {
        super();

        this.file = file;
    }

    get name() {
        return 'Import Project';
    }

    execute() {
        try {
            const importer = new ProjectImporter();
            importer.load(this.file, (data) => {
                const project = new Project(data.scene, data.settings);
                          
                data.envMap && Editor.world.setEnvironmentMap(data.envMap);
                
                project.envMap = data.envMap;

                Editor.project = project;
                
                EventAggregator.instance.notifyAll(new ProjectChangedEvent());

                Editor.frameScene();
            }, 
            err => EventAggregator.instance.notifyAll(new ErrorEvent(err)));
        }
        catch (e) {
            EventAggregator.instance.notifyAll(new ErrorEvent('An error occured loading the project file'));
        }
    }

    get undoable() { return false; }
}