import React, { Fragment } from 'react'
import Slider from '@material-ui/core/Slider';
import InputLabel from '@material-ui/core/InputLabel';
import PropTypes from 'prop-types';
import Divider from '@material-ui/core/Divider';

export default class EffectsView extends React.Component {
    constructor() {
        super();

        this.state = {};
    }

    render() {
        if (!this.props.viewModel || Object.keys(this.state).length === 0)
            return null;

        return (
            <Fragment>
                <div className='editor-sidebar-heading'>
                    Bloom Effect
                </div>
                <label className="checkboxLabel">Enabled
                    <input
                        type="checkbox"
                        checked={this.state.settings.bloom.enabled}
                        onChange={e => this.props.viewModel.onPropertyChanged('bloom.enabled', e.target.checked)} />
                    <span className="checkboxCheckmark"></span>
                </label>
                <InputLabel>Strength</InputLabel>
                <Slider
                    style={{ width: '200px' }}
                    value={this.state.settings.bloom.strength * 100}
                    onChange={(e, value) => this.props.viewModel.onPropertyChanged('bloom.strength', value / 100)} />
                <InputLabel>Threshold</InputLabel>
                <Slider
                    style={{ width: '200px' }}
                    value={this.state.settings.bloom.threshold * 100}
                    onChange={(e, value) => this.props.viewModel.onPropertyChanged('bloom.threshold', value / 100)} />
                <InputLabel>Radius</InputLabel>
                <Slider
                    style={{ width: '200px' }}
                    value={this.state.settings.bloom.radius * 100}
                    onChange={(e, value) => this.props.viewModel.onPropertyChanged('bloom.radius', value / 100)} />
                <div className='editor-sidebar-heading'>
                    Depth of Field
                </div>
                <label className="checkboxLabel">Enabled
                    <input type="checkbox" checked={this.state.settings.dof.enabled}
                        onChange={e => this.props.viewModel.onPropertyChanged('dof.enabled', e.target.checked)} />
                    <span className="checkboxCheckmark"></span>
                </label>
                <InputLabel>Focus</InputLabel>
                <Slider
                    style={{ width: '200px' }}
                    value={this.state.settings.dof.focus * 100}
                    onChange={(e, value) => this.props.viewModel.onPropertyChanged('dof.focus', value / 100)} />
                <InputLabel>Focal Length</InputLabel>
                <Slider
                    style={{ width: '200px' }}
                    value={this.state.settings.dof.focalLength * 100}
                    onChange={(e, value) => this.props.viewModel.onPropertyChanged('dof.focalLength', value / 100)} />
                <InputLabel>Blur</InputLabel>
                <Slider
                    style={{ width: '200px' }}
                    value={this.state.settings.dof.blur * 100}
                    onChange={(e, value) => this.props.viewModel.onPropertyChanged('dof.blur', value / 100)} />
                <Divider />
                <label className="checkboxLabel">Preview
                    <input type="checkbox" checked={this.state.viewState.preview}
                        onChange={e => this.props.viewModel.onViewStateChanged('preview', e.target.checked)} />
                    <span className="checkboxCheckmark"></span>
                </label>
            </Fragment>
        );
    }
}

EffectsView.propTypes = {
    viewModel: PropTypes.object,
}
