import { CommandManager } from "../commandManager";
import AddLightCommand from "../commands/addLightCommand";
import AddPrimitiveCommand from "../commands/addPrimitiveCommand";

export default class AddCommands {
    execute(name) {
        const type = name.split('.')[0];
        const objName = name.split('.')[1];

        let cmd;

        switch (type) {
            case 'primitive':
                cmd = new AddPrimitiveCommand(objName);
                break;
            case 'light':
                cmd = new AddLightCommand(objName);
                break;
        }

        cmd && CommandManager.execute(cmd);
    }
}